import React from "react";
import {Button, Layout, Menu, Space, Dropdown, Row, Col} from 'antd';
import store from "../store";

import {LogoutOutlined, DownOutlined} from '@ant-design/icons'

import {Dashboard} from "./dashboard";
import {Bucket} from "./bucket";
import {Mine} from "./mine";
import {Config} from "./config";
import {logOut} from "./funs";
import {ReactComponent as Logo} from '../logo.svg'

const {Header, Footer, Content} = Layout;

const menu = (
    <Menu>
        <Menu.Item>
            <Button style={{color:'black'}} type={'link'} onClick={()=>logOut()}><LogoutOutlined/>logout</Button>
        </Menu.Item>
        {/*<Menu.Item danger>a danger item</Menu.Item>*/}
    </Menu>
);

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.pages = {
            dashboard: <Dashboard />,
            bucket: <Bucket />,
            mine: <Mine />,
            config: <Config />
        }
        this.state = {
            content: this.pages.dashboard
        }
        this.globalState = store.getState()
        //
        // store.subscribe(() => {
        //     this.setState(store.getState());
        // })
    }

    render() {
        return (
            <Layout>
                {/*<Button*/}
                {/*    style={{*/}
                {/*        position: 'absolute',*/}
                {/*        right: 14,*/}
                {/*        top: 14,*/}
                {/*    }}*/}
                {/*    onClick={*/}
                {/*        () => {*/}
                {/*            window.sessionStorage.setItem('user_info', null);*/}
                {/*            window.location.reload();*/}
                {/*        }*/}
                {/*    }*/}
                {/*    icon={<LogoutOutlined />}*/}
                {/*    // shape={'circle'}*/}
                {/*/>*/}
                <Header className="header">
                    <Row>
                        <Col span={20}>
                            <Space align={'top'} size={0}>
                                {/*<img alt="logo" src={logo} width="55px" height="35px" />*/}
                                <Logo stroke="white" height={63} style={{marginTop:14}} />
                                <Menu theme="dark" mode="horizontal" onClick={this.handlerMenuClick} defaultSelectedKeys={'dashboard'}>
                                    <Menu.Item key="dashboard">Dashboard</Menu.Item>
                                    <Menu.Item key="bucket">Bucket</Menu.Item>
                                    {
                                        this.globalState.user_info.data.user_type === 'superuser' ? (<Menu.Item key="config">Config</Menu.Item>) : null
                                    }
                                    <Menu.Item key="mine">Mine</Menu.Item>
                                </Menu>
                            </Space>
                        </Col>

                        <Col span={4}>
                            <Dropdown
                                overlay={menu}
                            >
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <a
                                    href={'#'}
                                    style={{
                                        float:'right',
                                        color: '#fff'
                                    }}
                                    className="ant-dropdown-link"
                                    onClick={e => e.preventDefault()}
                                >
                                    {this.globalState.user_info.data.first_name} <DownOutlined />
                                </a>
                            </Dropdown>
                        </Col>
                    </Row>
                </Header>
                <Content id={"mainBody"} style={{minHeight: document.documentElement.clientHeight - (64+70)}}>
                    {this.state.content}
                </Content>
                <Footer style={{ textAlign: 'center' }}>FuRong Cloud Design 2021 Created by Ant UED</Footer>
            </Layout>
        );
    }

    handlerMenuClick=(e)=>{
        if (e.key in this.pages){
            this.setState({
                content: this.pages[e.key]
            })
            // store.dispatch({
            //     type: 'action_change_main_view',
            //     value: e.key
            // })
        }
    }
}

export default Home;
