import React from "react";
import {UserOutlined, LockOutlined, VerifiedOutlined} from '@ant-design/icons';
import store from "../store";
import {
    Form, Input, Button,
    Checkbox, Modal, Row,
    Col, Drawer, message
} from 'antd';
import axios from "axios";
import {ReactComponent as Logo} from '../logo.svg'
import ad from '../ad.png'
import {NewUser} from "./component/Users";

const css = {
    wrap: {
        width: "950px",
        margin: "auto",
        padding: "20px"
    },
    head: {
        width: "950px",
        height: "80px",
        paddingLeft: "20px"
        // background: "#F00"
    },
    left: {
        float: "left",
        width: "350px",
        padding: "25px",
    },
    right: {
        float: "left",
        width: "550px",
        height: "500px",
        padding: "25px",
        overflow: "hidden",
    }
}


class NormalLoginForm extends React.Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.drawerRef = React.createRef();
        this.remember = window.localStorage.getItem('remember') === '1'?true:false;

        this.formRef = React.createRef();

        this.login_info = {
            username: window.localStorage.getItem('username'),
            password: window.localStorage.getItem('password'),
            remember: this.remember,
        }

        this.state = {
            visible: false
        }
    }

    onRemember = (e) => {
        if (e.target.checked){
            window.localStorage.setItem('remember', '1');
        }else{
            window.localStorage.removeItem('remember');
        }

    }

    onFinish = (values) => {
        axios.post(
            `${this.globalState.domain}/api/user/login`,
            values
        ).then(
            res => {
                window.sessionStorage.setItem('user_info', JSON.stringify(res.data))
                if (values.remember) {
                    window.localStorage.setItem('username', values.username)
                    window.localStorage.setItem('password', values.password)
                }
                window.location.reload();
            }
        ).catch(
            err => {
                if (err.response) {
                    Modal.warning({
                        title: "login failed",
                        content: err.response.data.detail
                    })
                    return false;
                } else {
                    Modal.error({
                        title: "login failed",
                        content: 'connect to server is timeout!'
                    })
                    return false;
                }
            }
        );
    };

    render() {
        return (
            <Form
                name="login"
                className="login-form"
                ref={ref => this.formRef = ref}
                initialValues={{
                    remember: this.login_info.remember,
                    username: this.login_info.username,
                    password: this.login_info.password
                }}
                layout="vertical"
                onFinish={this.onFinish}
            >
                <Form.Item
                    name="username"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your Username!',
                        },
                    ]}
                    label="Username"
                >
                    <Input prefix={<UserOutlined/>} placeholder="Username"/>
                </Form.Item>
                <Form.Item
                    label="Password"
                    name="password"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your Password!',
                        },
                    ]}
                >
                    <Input
                        prefix={<LockOutlined/>}
                        type="password"
                        placeholder="******"
                    />
                </Form.Item>

                <Form.Item>
                    <Form.Item
                        label="Verification code"
                        name="verify_code"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your phone verification code!',
                            },
                        ]}
                    >
                        <Row gutter={6}>
                            <Col span={14}>
                                <Input
                                    prefix={<VerifiedOutlined/>}
                                    placeholder="verification code"
                                />
                            </Col>
                            <Col span={10}>
                                <Button
                                    type="primary"
                                    style={{padding: '4px 12px'}}
                                    onClick={
                                        () => {
                                            let username = this.formRef.getFieldValue('username')
                                            if (username === null || username.trim().length === 0) {
                                                message.info('please input your username')
                                                return;
                                            }
                                            axios.get(
                                                this.globalState.domain + '/api/user/send_verify_code', {
                                                    params: {
                                                        username: username
                                                    }
                                                }
                                            ).then(
                                                () => message.info("message sent successfully")
                                            ).catch(
                                                e => {
                                                    if (e.response) {
                                                        message.error(e.response.data.detail)
                                                    }
                                                }
                                            )
                                        }
                                    }
                                >Send Message</Button>
                            </Col>
                        </Row>
                    </Form.Item>
                </Form.Item>

                <Form.Item>
                    <Form.Item name="remember" valuePropName="checked" noStyle>
                        <Checkbox onChange={this.onRemember}>Remember me</Checkbox>
                    </Form.Item>
                </Form.Item>

                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        Log in
                    </Button>
                    &nbsp;Or <Button type={'link'} style={{padding: 0}} onClick={
                    () => this.setState({visible: true})
                }>register now!</Button>
                </Form.Item>

                <Drawer
                    visible={this.state.visible}
                    width="400px"
                    title={'User register'}
                    placement="right"
                    closable={true}
                    onClose={
                        () => {
                            this.setState({
                                visible: false,
                            })
                        }
                    }
                    footer={
                        (<div style={{textAlign: 'right'}}>
                            <Button onClick={() => this.drawerRef.submit()} type="primary">
                                Submit
                            </Button>
                        </div>)
                    }>
                    <NewUser onSuccess={()=>this.setState({visible: false})} ref={ref => this.drawerRef = ref}/>
                </Drawer>
            </Form>
        )
    }
}


class Login extends React.Component {

    render() {
        return (
            <div style={css.wrap}>
                <div style={css.head}>
                    <Logo width={80} stroke="#a80"/>
                </div>
                <div style={css.left}>
                    <NormalLoginForm/>
                </div>
                <div style={css.right}>
                    <img alt={"logo"} src={ad}/>
                </div>
            </div>
        )
    }
}

export default Login