import React, {Component} from "react";
import {
    Table, Button, Card,
    Drawer, Divider, Input,
    Row, Col
} from 'antd';

import store from "../../store";
import axios from "axios";

const {Search} = Input

const OrderDetail=(props)=>{
    let styles={
        field:{
            width: '100px',
            display: "inline-block",
            textAlign: 'right',
            marginRight: '20px'
        }
    }
    return (
        <div>
            <p><b style={styles.field}>orderNo.</b>{props.data.no}</p>
            <p><b style={styles.field}>product</b>{props.data.en_product}</p>
            <p><b style={styles.field}>detail</b>{props.data.detail}</p>
            <p><b style={styles.field}>create date</b>{props.data.create_date.substr(0,19)}</p>
            <Divider/>
            <p><b style={styles.field}>plan</b>{props.data.plan.name}</p>
            <p><b style={styles.field}>play</b>{props.data.pay}</p>
            <p><b style={styles.field}>real play</b>{props.data.real_pay}</p>
        </div>
    )
}

export class OrderList extends Component{
    constructor(props) {
        super(props);
        this.globalState=store.getState()
        this.state={
            data_list:[],
            visible: false,
            order_detail:{},
            page_info: {},
            drawer_content: null,
        }
    }

    componentDidMount() {
        this.getUserOrders()
    }

    getUserOrders =(p=1, orderNo='')=>{
        let url = this.globalState.domain+'/api/account/order?page='+p
        if (orderNo.trim().length > 1) {
            url += '&order_no='+orderNo
        }

        axios.get(
            url,
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(ret=>{
            this.setState({
                data_list: ret.data.data,
                page_info: ret.data.page_info
            })
        }).catch(
            e=>{
                if (e.response && e.response.status === 403){
                    window.sessionStorage.setItem('user_info', null);
                }
            }
        )
    }

    handlePageChange =(p)=>{
        this.getUserOrders(p)
    }

    handleOrderDetail = (record)=>{
        this.setState({
            visible: true,
            drawer_content: <OrderDetail data={record} />
        })
    }

    orderColumns=[
        {
            title: 'OrderNo.',
            dataIndex: 'no',
        },
        {
            title: 'Product',
            dataIndex: 'en_product',
        },
        {
            title: 'Ceate time',
            dataIndex: 'create_date',
            render: (value)=>{
                return value.substr(0, 16)
            }
        },
        {
            title: 'Detail',
            dataIndex: 'id',
            render: (value, record) => {
                return <Button style={{margin:0,padding:0}} type={'link'} onClick={e=>{
                    this.handleOrderDetail(record)
                }}>Detail</Button>
            }
        }
    ]

    onSearch=(e)=>{
        this.getUserOrders(1, e)
    }

    render() {
        return (
            <Card title={'Order List'}>
                <Row gutter={[8,8]}>
                    <Col span={8}>
                        <Search placeholder="input order No." onSearch={this.onSearch} enterButton />
                    </Col>
                    <Col span={24}>
                        <Table
                            columns={this.orderColumns}
                            dataSource={this.state.data_list}
                            rowKey={record=>record.id}
                            pagination={
                                {
                                    current: this.state.page_info.current_page,
                                    pageSize: this.state.page_info.page_size,
                                    total: this.state.page_info.record_count,
                                    onChange: this.handlePageChange,
                                }
                            }
                        />
                    </Col>
                </Row>

                <Drawer
                    title={'Order Detail'}
                    width={'400px'}
                    visible={this.state.visible}
                    placement="right"
                    closable={true}
                    onClose={()=>{
                        this.setState({
                            visible: false,
                            drawer_content: null
                        })
                    }}
                        // footer={this.state.drawer_foot}
                >
                    {this.state.drawer_content}
                </Drawer>
            </Card>
        );
    }

}