import React, {Component, createRef} from "react";
import {
    Table, Button,
    message, Card, Drawer,
    Form, InputNumber, Radio,
    Input, Modal, Badge, Space,
} from "antd";

import {ExclamationCircleOutlined, PlusOutlined, EditOutlined, DeleteOutlined} from "@ant-design/icons";
import store from "../../store";
import axios from "axios";
import {toPercent} from "../funs";

class NewPlan extends Component {
    constructor(props) {
        super(props);
        this.formRef = createRef();

        if (this.props.formData === undefined) {
            this.formId = 0
        } else {
            this.formId = this.props.formData.id
        }
    }

    submit = () => {
        this.formRef.submit();
    }

    render() {
        return (
            <Form
                layout={'vertical'}
                onFinish={v => this.props.onFinish(v, this.formId)}
                ref={ref => this.formRef = ref}
                initialValues={
                    this.props.formData
                }
            >
                <Form.Item
                    name={'s_price'}
                    label={'Storage Price (per day)'}
                    rules={[
                        {
                            required: true,
                            message: 'please input storage price'
                        }
                    ]}
                >
                    <InputNumber
                        // style={{ width: 200 }}
                        // defaultValue={this.props.s_price}
                        min="0"
                        max="10"
                        step="0.01"
                        onChange={v => this.formRef.setFieldsValue({s_price: parseFloat(v)})}
                        stringMode
                    />
                </Form.Item>

                <Form.Item
                    name={'b_price'}
                    label={'Bandwidth Price (per day)'}
                    rules={[
                        {
                            required: true,
                            message: 'please input bandwidth price'
                        }
                    ]}
                >
                    <InputNumber
                        // style={{ width: 200 }}
                        // defaultValue="1"
                        min="0"
                        max="10"
                        step="0.01"
                        onChange={v => this.formRef.setFieldsValue({b_price: parseFloat(v)})}
                        stringMode
                    />
                </Form.Item>

                <Form.Item
                    name={'plan_min_days'}
                    label={'Plan min require days (day)'}
                    rules={[
                        {
                            required: true,
                            message: 'please input plan min require days'
                        }
                    ]}
                >
                    <InputNumber
                        // style={{ width: 200 }}
                        // defaultValue="1"
                        min="1"
                        max="9999"
                        step="30"
                        parser={value => parseInt(value)}
                        onChange={v => this.formRef.setFieldsValue({plan_min_days: parseInt(v)})}
                        stringMode
                    />
                </Form.Item>


                <Form.Item
                    name={'offset'}
                    label={'Offset'}
                    rules={[
                        {
                            required: true,
                            message: 'please input storage price'
                        }
                    ]}
                >
                    <InputNumber
                        // style={{ width: 200 }}
                        // defaultValue={100}
                        min={0}
                        max={1}
                        step={0.01}
                        formatter={value => toPercent(value)}
                        parser={value => parseInt(value.replace('%', '')) / 100}
                        onChange={v => this.formRef.setFieldsValue({offset: parseFloat(v)})}
                        stringMode
                    />
                </Form.Item>


                <Form.Item
                    name={'offset_min_days'}
                    label={'Offset min require days (day)'}
                    rules={[
                        {
                            required: true,
                            message: 'please input storage price'
                        }
                    ]}
                >
                    <InputNumber
                        // style={{ width: 200 }}
                        // defaultValue="1"
                        min="1"
                        max="9999"
                        step="30"
                        onChange={v => this.formRef.setFieldsValue({offset_min_days: parseInt(v)})}
                        stringMode
                    />
                </Form.Item>

                <Form.Item
                    name={'name'}
                    label={'Name'}
                    rules={[
                        {
                            required: true,
                            message: 'please input plan name!',
                            max: 50,
                        }
                    ]}
                >
                    <Input
                        // style={{ width: 200 }}
                        // defaultValue="1"
                        placehold={'plan name'}
                    />
                </Form.Item>

                <Form.Item
                    name={'state'}
                    label={'State'}
                    rules={[
                        {
                            required: true,
                            message: 'please choice region state!',
                            max: 1
                        }
                    ]}
                >
                    <Radio.Group buttonStyle="solid" style={{width: '100%', textAlign: 'center'}}>
                        <Radio.Button value="e" style={{width: '50%'}}>Enable</Radio.Button>
                        <Radio.Button value="d" style={{width: '50%'}}>Disable</Radio.Button>
                    </Radio.Group>
                </Form.Item>
            </Form>
        )
    }
}

export class Plan extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.Child = React.createRef();
        this.state = {
            data: [],
            drawer_title: '',
            visible: false,
            drawer_content: ''
        }
    }

    componentDidMount() {
        this.getPlanList();
    }

    getPlanList = () => {
        axios.get(
            this.globalState.domain + '/api/account/plan', {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret => this.setState({
                data: ret.data.data
            })
        ).catch(e => {
            if (e.response && e.response.status === 403) {
                window.sessionStorage.setItem('user_info', null);
                window.location.reload();
            } else {
                message.error(e.response.data.detail)

            }
        })
    }

    planColumns = [
        {title: 'Name', dataIndex: 'name',},
        {title: 'Storage Price', dataIndex: 's_price',},
        {title: 'Bandwidth Price', dataIndex: 'b_price',},
        {title: 'Plan Min Days', dataIndex: 'plan_min_days',},
        {title: 'Offset', dataIndex: 'offset', render: (v) => toPercent(v)},
        {title: 'Offset Min Days', dataIndex: 'offset_min_days',},
        {
            title: 'Status',
            dataIndex: 'state',
            render: (v) => {
                return v === 'e' ? <Badge status="processing" text="Enable" /> : <Badge status="error" text="Disable" />
            }
        },
        {
            title: 'Action',
            dataIndex: 'id',
            render: (v, record) => {
                return (
                    <Space>
                        <Button icon={<EditOutlined/>} onClick={e => this.handleBtnClick('editPlan', record)}>Edit</Button>
                        <Button icon={<DeleteOutlined/>} onClick={e => this.handleBtnClick('deletePlan', record)}>Delete</Button>
                    </Space>
                )
            }
        }

    ]

    drawerClose = (e) => {
        this.setState({
            drawer_title: null,
            drawer_content: null,
            visible: false
        })
        this.getPlanList();
    }

    formFinish = (values, id) => {
        let url = this.globalState.domain + '/api/account/plan'
        let result = null
        if (id === 0) {
            result = axios.post(
                url,
                values,
                this.globalState.request_headers
            )
        }

        if (id > 0) {
            values.id = id
            result = axios.put(
                url,
                values,
                this.globalState.request_headers
            )
        }

        if (result != null) {
            result.then(ret => message.success('success')).catch(
                e => {
                    if (e.response && e.response.data.status === 403) {
                        window.sessionStorage.setItem('user_info', null);
                        window.location.reload()
                    } else {
                        message.warning(e.response.data.detail)
                    }
                }
            )
        }

    }

    deletePlan = (id) => {
        axios.delete(
            this.globalState.domain + '/api/account/plan?id=' + id,
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => {
            message.success('success')
            this.getPlanList();
        }).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload()
                } else {
                    message.warning(e.response.data.detail)
                }
            }
        )
    }
    onSubmit = () => {
        this.Child.submit();
    }

    handleBtnClick = (e, v = undefined) => {
        // eslint-disable-next-line default-case
        switch (e) {
            case 'newPlan':
                this.setState({
                    drawer_content: <NewPlan ref={ref => this.Child = ref} onFinish={this.formFinish}/>,
                    drawer_title: 'New Plan',
                    visible: true,
                })
                break;
            case 'editPlan':
                this.setState({
                    drawer_content: <NewPlan ref={ref => this.Child = ref} formData={v} onFinish={this.formFinish}/>,
                    drawer_title: 'Edit Plan',
                    visible: true,
                })
                break;
            case 'deletePlan':
                Modal.confirm({
                    icon: <ExclamationCircleOutlined />,
                    title: "warning",
                    content: 'Deleting plan will be destroy all related order',
                    okType: 'danger',
                    onOk:()=>{
                        this.deletePlan(v.id);
                    },
                })
                break;
        }
    }

    render() {
        return (
            <Card title={'Plan & Price List'}>
                <Button type={'primary'} style={{marginBottom: 10}} onClick={e => this.handleBtnClick('newPlan')}>
                    <PlusOutlined/>New Plan
                </Button>
                <Table dataSource={this.state.data} columns={this.planColumns} rowKey={record => record.id}/>
                <Drawer
                    title={this.state.drawer_title}
                    width={400}
                    onClose={this.drawerClose}
                    visible={this.state.visible}
                    closable={true}
                    footer={(<div style={{textAlign: 'right'}}>
                        <Button onClick={this.onSubmit} type="primary">
                            Submit
                        </Button>
                    </div>)}
                >
                    {this.state.drawer_content}
                </Drawer>
            </Card>
        )
    }
}