import {Upload, Radio, Alert, message, Space} from "antd";
import React from "react";

import {InboxOutlined} from "@ant-design/icons";

import store from "../../store";

const {Dragger} = Upload;

export class FileUpload extends React.Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();

        this.explain = {
            private: 'Only the owner of the bucket can access it',
            public_read_write: 'Danger: Anybody can read and write data to this bucket!!!',
            public_read: 'Anybody can read data from this bucket',
            authenticated: 'Only the owner and authorized person can access it',
            inherit: 'Inherit the permissions of the bucket'
        }

        this.state = {
            upload_url: this.globalState.domain + '/api/objects/upload_file',
            permission: '',
            permission_explain: this.explain.inherit,
        }
    }

    onChange = (info) => {
        // console.log(info)
        const {status} = info.file;
        // if (status !== 'uploading') {
        //     console.log(info.file, info.fileList);
        // }
        if (status === 'done') {
            message.success(`${info.file.name} file uploaded successfully.`);
            this.props.onSuccess();
        } else if (status === 'error') {
            // message.error(`${info.file.name} file upload failed.`);
            message.error(info.file.response.detail);
        }
    }

    submit = (e) => {
        console.log(e)
    }

    handlePermissionChange = (e) => {
        if (e.target.value) {
            this.setState({
                permission: e.target.value === 'inherit' ? '' : e.target.value,
                permission_explain: this.explain[e.target.value.replaceAll('-', '_')]
            })
        }
    }

    render() {
        return (
            <Space direction={'vertical'}>
                <Dragger
                    multiple={true}
                    name={"file"}
                    action={this.state.upload_url}
                    method={'PUT'}
                    onChange={this.onChange}
                    // customRequest={this.submit}
                    // onDrop={this.onDrop}
                    headers={this.globalState.request_headers.headers}
                    showUploadList={
                        {
                            showPreviewIcon: false,
                            showRemoveIcon: false,
                            showDownloadIcon: true,
                            removeIcon: false,
                            downloadIcon: true
                        }
                    }
                    data={
                        () => {
                            return {
                                bucket_name: this.props.name,
                                path: this.props.path,
                                permission: this.state.permission,
                            }
                        }
                    }
                >
                    <p style={{fontSize: "12px"}} className="ant-upload-drag-icon">
                        <InboxOutlined/>
                    </p>
                    <p style={{fontSize: "12px"}} className="ant-upload-text">Click or drag file to this area to
                        upload</p>
                    <p style={{fontSize: "12px"}} className="ant-upload-hint">
                        Support for a single or bulk upload.
                    </p>
                </Dragger>

                <Radio.Group defaultValue={"inherit"} buttonStyle="solid" size={"small"}
                             style={{width: '100%', textAlign: 'center'}} onChange={this.handlePermissionChange}>
                    <Radio.Button value="inherit">Inherit</Radio.Button>
                    <Radio.Button value="private">Private</Radio.Button>
                    <Radio.Button value="public-read">Public-read</Radio.Button>
                    <Radio.Button value="public-read-write">Public-read-write</Radio.Button>
                    <Radio.Button value="authenticated">Authenticated</Radio.Button>
                </Radio.Group>
                <Alert type={"info"} message={'Notice'} description={
                    <div style={{fontSize: "12px"}}>
                        <p style={{margin: '0'}}>{this.state.permission_explain}</p>
                    </div>
                }/>
            </Space>
        )
    }
}