import React, {Component} from "react";
import {
    Card,
} from "antd";
import store from "../../store";
import axios from "axios";

export class License extends Component{
    constructor(props) {
        super(props);
        this.globalState=store.getState();
        this.state={
            data:[],
            drawer_content: ''
        }

        this.styles = {
            itemField: {
                width: '100px',
                display: "inline-block",
                textAlign: 'right',
                marginRight: '20px',
            }
        }
    }

    getLocalTime = (nS) => {
        return new Date(parseInt(nS) * 1000).toLocaleString('en-Us');
    }

    componentDidMount() {
        this.getLicenseInfo();
    }

    getLicenseInfo=()=>{
        axios.get(
            this.globalState.domain+'/api/license', {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret=>this.setState({
                data: ret.data.license
            })
        )
    }

    render() {
        return (
            <Card title={'License info'}>
                <p>
                    <b style={this.styles.itemField}>Information</b>
                    {this.state.data.info}
                </p>
                <p>
                    <b style={this.styles.itemField}>Max Users</b>
                    {this.state.data.max_users}
                </p>
                <p>
                    <b style={this.styles.itemField}>Max Buckets</b>
                    {this.state.data.max_bucket}
                </p>
                <p>
                    <b style={this.styles.itemField}>Max Capacity</b>
                    {this.state.data.max_capacity} G
                </p>
                <p>
                    <b style={this.styles.itemField}>Start Time</b>
                    {this.getLocalTime(this.state.data.start_time)}
                </p>
                <p>
                    <b style={this.styles.itemField}>End Time</b>
                    {this.getLocalTime(this.state.data.end_time)}
                </p>

                <p>
                    <b style={this.styles.itemField}>Status</b>
                    {this.state.data.expire ? 'Expired' : 'Normal' }
                </p>
            </Card>
        )
    }
}