import React, {Component} from "react";
import {
    Card, Input, Row, Col
} from "antd";
import {Users} from "./Users";

const {Search} = Input

export class UserManage extends Component{
    constructor(props) {
        super(props);
        this.child = React.createRef();
    }

    onSearch=(e)=>{
        this.child.getUserList(1, e)
    }

    render() {
        return (
            <Card title={'User manage'}>
                <Row gutter={[8,8]}>
                    <Col span={8}>
                        <Search placeholder="input username" onSearch={this.onSearch} enterButton />
                    </Col>
                    <Col span={24}>
                        <Users type={'superuser'} ref={ref=>this.child=ref} />
                    </Col>
                </Row>

            </Card>
        )
    }
}