import {Alert, message, Input, Space} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";


export class Delete extends React.Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state={
            inputValue:'',
        }
    }

    submit = () => {
        if (this.state.inputValue !== this.props.name){
            return message.error('bucket name is incorrect!');
        }
        axios.delete(
            this.globalState.domain + '/api/buckets/bucket', {
                data: {
                    bucket_id: this.props.id
                },
                headers: this.globalState.request_headers.headers
            },
        ).then(
            () => {
                message.info('success');
                this.props.onSuccess();
            }
        ).catch(
            e => message.warning(e.response.data.detail)
        )
    }

    onChange=(e)=>{
        this.setState({
            inputValue: e.target.value
        })
    }

    render() {
        return (
            <Space direction={'vertical'}>
                <Alert
                    showIcon
                    type="error"
                    message={"Warning"}
                    description="Deleting the bucket will delete all the files in the bucket, and the files cannot be recovered after being deleted"
                />
                <label>Bucket Name</label>
                <Input placeholder={this.props.name} onChange={this.onChange} />
            </Space>

        )
    }
}
