class NewRegion extends Component {
    constructor(props) {
        super(props);
        this.formRef = createRef();
        this.type_explain = {
            amazon: 'https://s3.region-code.amazonaws.com',
            local: 'ceph rgw gateway address'
        }
        this.state={
            endpoint_text: this.props.formData ? this.type_explain[this.props.formData.type] : this.type_explain.local
        }

        if (this.props.formData !== undefined && this.props.formData.reg_id > 0) {
            this.reg_id = this.props.formData.reg_id
        } else {
            this.reg_id = 0
        }
    }

    submit = () => {
        this.formRef.submit()
    }

    regionTypeChange=(v)=>{
        if (v==='amazon'){
            this.setState({endpoint_text: this.type_explain.amazon})
        }
        if (v==='local'){
            this.setState({endpoint_text: this.type_explain.local})
        }
    }

    render() {
        return (
            <Form
                layout={'vertical'}
                onFinish={(v) => this.props.onFinish(v, this.reg_id)}
                ref={ref => this.formRef = ref}
                initialValues={
                    this.props.formData
                }
            >
                <Form.Item
                    name={'name'}
                    label={'Name'}
                    rules={[
                        {
                            required: true,
                            message: 'please input region name!',
                            max: 40
                        }
                    ]}
                >
                    <Input placeholder={'region name'}/>
                </Form.Item>

                <Form.Item
                    name={'access_key'}
                    label={'Access Key'}
                    rules={[
                        {
                            required: true,
                            message: 'please input region name!',
                            max: 30
                        }
                    ]}
                >
                    <Input placeholder={'access key'}/>
                </Form.Item>

                <Form.Item
                    name={'secret_key'}
                    label={'Secret Key'}
                    rules={[
                        {
                            required: true,
                            message: 'please input region name!',
                            max: 40
                        }
                    ]}
                >
                    <Input placeholder={'secret key'}/>
                </Form.Item>

                <Form.Item
                    name={'server'}
                    label={'Endpoint'}
                    rules={[
                        {
                            required: true,
                            message: 'please backend endpoint address!',
                            max: 50
                        }
                    ]}
                >
                    {/*<Input addonBefore={selectBefore} placeholder={'server address'} />*/}
                    <Space direction={'vertical'} style={{width: '100%'}}>
                        <Input placeholder={'server address'}/>
                        <Alert type={'info'} message={this.state.endpoint_text}/>
                    </Space>

                </Form.Item>

                <Form.Item
                    initialValue={'local'}
                    name={'type'}
                    label={'Type'}
                    rules={[
                        {
                            required: true,
                            message: 'please choice type',
                            max: 10
                        }
                    ]}
                >
                    {/*<Input addonBefore={selectBefore} placeholder={'server address'} />*/}
                    <Select onChange={this.regionTypeChange}>
                        <Select.Option value={'local'}>Local</Select.Option>
                        <Select.Option value={'amazon'}>Amazon</Select.Option>
                    </Select>
                </Form.Item>

                <Form.Item
                    name={'state'}
                    label={'State'}
                    rules={[
                        {
                            required: true,
                            message: 'please choice region state!',
                            max: 1
                        }
                    ]}
                >
                    <Radio.Group buttonStyle="solid" style={{width: '100%', textAlign: 'center'}}>
                        <Radio.Button value="e" style={{width: '50%'}}>Enable</Radio.Button>
                        <Radio.Button value="d" style={{width: '50%'}}>Disable</Radio.Button>
                    </Radio.Group>
                </Form.Item>
            </Form>
        )
    }
}


export class Region extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.Child = React.createRef();
        this.state = {
            data: [],
            drawer_title: '',
            visisble: false,
            drawer_content: ''
        }
    }

    componentDidMount() {
        this.getRegionList();
    }

    getRegionList = () => {
        axios.get(
            this.globalState.domain + '/api/buckets/region',
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => this.setState({
                data: ret.data.data
            })
        )
    }

    regionColumns = [
        {title: 'Name', dataIndex: 'name',},
        {title: 'Access Key', dataIndex: 'access_key',},
        {title: 'Secret Key', dataIndex: 'secret_key',},
        {title: 'Endpoint', dataIndex: 'server',},
        {title: 'Type', dataIndex: 'type',},
        {
            title: 'Status',
            dataIndex: 'state',
            render: (v=>{
                return v==='e' ? <Badge status="processing" text="Enable" /> : <Badge status="error" text="Disable" />
            })
        },
        {
            title: 'Action',
            dataIndex: 'reg_id',
            render: (v, record) => {
                return (
                    <Space>
                        <Button icon={<EditOutlined/>} onClick={e => this.handleEditRegionEvent(e, record)}>Edit</Button>
                        <Button icon={<DeleteOutlined/>} onClick={() => this.handleDeleteRegionEvent(v)}>Delete</Button>
                    </Space>
                )
            }
        }

    ]

    drawerClose = () => {
        this.setState({
            drawer_title: null,
            drawer_content: null,
            visible: false
        })
        // this.getRegionList();
    }


    handleEditRegionEvent = (e, record) => {
        this.setState({
            drawer_title: 'Edit region',
            drawer_content: <NewRegion ref={ref => this.Child = ref} formData={record} onFinish={this.onFinish}/>,
            visible: true
        })
    }

    handleDeleteRegionEvent = (v) => {

        Modal.confirm({
            icon: <ExclamationCircleOutlined />,
            title: "warning",
            okType: 'danger',
            content: 'Deleting region will delete all related buckets and objects',
            onOk:()=>{
                axios.delete(
                    this.globalState.domain + '/api/buckets/region',
                    {
                        data: {
                            reg_id: v
                        },
                        headers: this.globalState.request_headers.headers
                    }
                ).then(() => {
                    message.success('success');
                    this.getRegionList();
                }).catch(
                    e => {
                        if (e.response && e.response.data.status === 403) {
                            window.sessionStorage.setItem('user_info', null);
                            window.location.reload();
                        } else {
                            // console.log(e.response)
                            message.warning(e.response.data.detail)
                        }
                    }
                )
            },
        })

    }

    handleAddRegionEvent = (e) => {
        this.setState({
            drawer_title: 'Add a new region',
            drawer_content: <NewRegion ref={ref => this.Child = ref} onFinish={this.onFinish}/>,
            visible: true
        })
    }

    onFinish = (values, id) => {
        let url = this.globalState.domain + '/api/buckets/region';
        let result = null;

        if (id > 0) {
            values.reg_id = id
            result = axios.put(
                url,
                values,
                this.globalState.request_headers
            )
        }

        if (id === 0) {
            result = axios.post(
                url,
                values,
                this.globalState.request_headers
            )
        }

        if (result !== null) {
            result.then(
                () => {
                    message.success('success');
                    this.setState({visible: false})
                    this.getRegionList();
                }
            ).catch(e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail)
                }
            })
        }
    }

    onSubmit = () => {
        this.Child.submit()
    }

    render() {
        return (
            <Card title={'Region List'}>
                <Button type={'primary'} style={{marginBottom: 10}} onClick={this.handleAddRegionEvent}><PlusOutlined/>Add
                    Region</Button>
                <Table dataSource={this.state.data} columns={this.regionColumns} rowKey={record => record.reg_id}/>
                <Drawer
                    title={this.state.drawer_title}
                    onClose={this.drawerClose}
                    visible={this.state.visible}
                    width={500}
                    closable={true}
                    footer={(<div style={{textAlign: 'right'}}>
                        <Button onClick={this.onSubmit} type="primary">
                            Submit
                        </Button>
                    </div>)}
                >
                    {this.state.drawer_content}
                </Drawer>
            </Card>
        )
    }
}