import {Form, Input, message, Radio, Select, Alert, Space} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";


export class NewBucket extends React.Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.explain = {
            private: 'Only the owner of the bucket can access it',
            public_read_write: 'Danger: Anybody can read and write data to this bucket!!!',
            public_read: 'Anybody can read data from this bucket',
            authenticated: 'Only the owner and authorized person can access it'
        }
        this.state = {
            region_list: [],
            permission_explain: this.explain.private
        }
        this.formRef = React.createRef();
        this.getRegionList();
    }

    handlePermBtnClick = (e) => {
        this.setState({
            permission_explain: this.explain[e.target.value.replaceAll('-','_')]
        })
    }

    submit = () => {
        this.formRef.submit();
    }

    onFinish = (values)=>{
        if (values.bucket_region_id === undefined || values.name === undefined){
            message.error("no bucket name or not select region")
            return false;
        }
        axios.post(
            this.globalState.domain+'/api/buckets/bucket',
            values,
            this.globalState.request_headers
        ).then(
            ()=>{
                message.info('success');
                this.props.onSuccess();
            }
        ).catch(
            e=>message.warning(e.response.data.detail)
        )

    }

    getRegionList = () => {
        axios.get(
            `${this.globalState.domain}/api/buckets/region`,
            this.globalState.request_headers
        ).then((res) => {
            this.setState({
                region_list: res.data.data
            })
        })
    }

    renderRegionOption = () => {
        return this.state.region_list.map(item =>
            <Select.Option key={item.reg_id} value={item.reg_id}>{item.name}</Select.Option>);
    }


    render() {
        return (
            <Form
                layout="vertical"
                ref={ref=>this.formRef=ref}
                // initialValues={
                //     {
                //         permission: 'private',
                //         version_control: false
                //     }
                // }
                onFinish={this.onFinish}
            >
                <Form.Item
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: 'Please input your bucket name!',
                            max: 64
                        },
                    ]}
                    label="Name"
                >
                    <Space direction={'vertical'}>
                        <Input placeholder="mybucket"/>
                        <Alert type={"info"} message={'Notice'} description={
                            <div style={{fontSize:"12px"}}>
                                <p style={{margin:'0'}}>1-63 English characters, cannot start with a number.</p>
                                <p style={{margin:'0'}}>The name is globally unique and must not contain spaces and cannot be changed after creation.</p>
                            </div>
                        } />
                    </Space>
                </Form.Item>

                <Form.Item
                    name="bucket_region_id"
                    label="Region"
                    rules={[
                        {
                            required: true,
                            message: 'Please choice you region!',
                        },
                    ]}
                >
                    <Select placeholder="region">
                        {this.renderRegionOption()}
                    </Select>
                </Form.Item>

                <Form.Item
                    name="permission"
                    label="Permission"
                    initialValue={'private'}
                    rules={[
                        {
                            required: true,
                            message: 'Please choice your permission!',
                        },
                    ]}
                >
                    <Space direction={'vertical'} style={{width: '100%'}}>
                        <Radio.Group defaultValue="private" buttonStyle="solid" size={'small'} onChange={this.handlePermBtnClick} style={{width: '100%', textAlign:"center"}}>
                            <Radio.Button style={{width: "15%"}} value="private">Private</Radio.Button>
                            <Radio.Button style={{width: "25%"}} value="public-read">Public-read</Radio.Button>
                            <Radio.Button style={{width: "35%"}} value="public-read-write">Public-read-write</Radio.Button>
                            <Radio.Button style={{width: "25%"}} value="authenticated">Authenticated</Radio.Button>
                        </Radio.Group>
                        <Alert type={"info"} message={'Notice'} description={
                            <div style={{fontSize:"12px"}}>
                                <p style={{margin:'0'}}>{this.state.permission_explain}</p>
                            </div>
                        } />
                    </Space>

                </Form.Item>

                <Form.Item name="version_control" initialValue={false}  label="Versioning" style={{marginTop: -24}}>
                    <Space direction={'vertical'} style={{width: '100%'}}>
                        <Radio.Group defaultValue={false} buttonStyle="solid" size={'small'} style={{width: '100%', textAlign: 'center'}}>
                            <Radio.Button style={{width: '50%'}}  value={true}>Enable</Radio.Button>
                            <Radio.Button style={{width: '50%'}}  value={false} defaultChecked>Disable</Radio.Button>
                        </Radio.Group>
                        <Alert type={"info"} message={'Notice'} description={
                            <div style={{fontSize:"12px"}}>
                                <p style={{margin:'0'}}>
                                    Versioning is a means of keeping multiple variants of an object in the same bucket.
                                    You can use versioning to preserve, retrieve, and restore every version of every object stored in your bucket.
                                    With versioning, you can easily recover from both unintended user actions and application failures.
                                </p>
                            </div>
                        } />
                    </Space>
                </Form.Item>
            </Form>
        );
    }
}