class UserPlainSelect extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            region_list: []
        }

        this.styles = {
            itemField: {
                // width: '50',
                display: "inline-block",
                textAlign: 'right',
                marginRight: '20px',
                fontSize: '12px'
            }
        }
    }

    componentDidMount() {
        this.getAllRegionList();
    }

    getAllRegionList = () => {
        axios.get(
            this.globalState.domain + '/api/account/plan', {
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => this.setState({
            region_list: ret.data.data
        })).catch(e => {
            if (e.response && e.response.status === 403) {
                window.sessionStorage.setItem('user_info', null);
            }
        })
    }

    render() {
        return (
            <Radio.Group buttonStyle="solid" style={{marginTop: 16}} onChange={this.props.planChange}>
                <Row gutter={8}>
                    {
                        this.state.region_list.map((item, index) => {
                            if (item.state !== 'e') {
                                return null;
                            }
                            return <Col span={12} key={'plan_detail_' + index}>
                                <Row gutter={8}>
                                    <Col span={24}>
                                        <Card title={item.name} style={{marginBottom: 8}}>
                                            <p>
                                                <b style={this.styles.itemField}>price</b><br/>
                                                &nbsp;{this.props.type === 'storage' ? item.s_price : item.b_price}/d<br/>
                                                <b style={this.styles.itemField}>Min purchase days</b><br/>
                                                &nbsp;{item.plan_min_days}<br/><br/>
                                                <b style={this.styles.itemField}>offset</b><br/>
                                                &nbsp;{toPercent(item.offset)}<br/>
                                                <b style={this.styles.itemField}>Min purchase days</b><br/>
                                                &nbsp;{item.offset_min_days}/d
                                            </p>
                                        </Card>
                                    </Col>
                                    <Col span={24} style={{textAlign: 'center'}}>
                                        <Radio.Button value={item.id}><CheckOutlined/></Radio.Button>
                                    </Col>
                                </Row>
                            </Col>
                        })
                    }
                </Row>
            </Radio.Group>
        )
    }


}

class BandwidthQuota extends Component {
    constructor(props) {
        super(props);
        this.formRef = createRef();
    }

    submit = () => {
        this.formRef.submit()
    }

    render() {
        return (
            <Form
                ref={ref => this.formRef = ref}
                layout={'vertical'}
                onFinish={this.props.onFinish}
            >
                <Form.Item
                    name={'bandwidth'}
                    label={'Bandwidth (M)'}
                    rules={[
                        {
                            required: true,
                            message: 'please set you bandwidth'
                        }
                    ]}
                >
                    <InputNumber min={5} max={100} onChange={e => this.formRef.setFieldsValue({capacity: e})}/>
                </Form.Item>

                <Form.Item
                    name={'duration'}
                    label={'Duration (Month)'}
                    rules={[
                        {
                            required: true,
                            message: 'please set you duration'
                        }
                    ]}
                >
                    <QuotaDays onChange={e => {
                        this.formRef.setFieldsValue({duration: e.target.value})
                    }}/>
                </Form.Item>

                <Form.Item
                    name={'plan_id'}
                    label={'Plan'}
                    rules={[
                        {
                            required: true,
                            message: 'please choice your plan'
                        }
                    ]}
                >
                    <UserPlainSelect type={'bandwidth'} planChange={e => {
                        this.formRef.setFieldsValue({plan_id: e.target.value})
                    }}/>
                </Form.Item>
            </Form>
        )
    }
}

