export class ChangePassword extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.formRef = React.createRef()
    }

    submit = () => {
        this.formRef.submit()
    }

    OnFinished = (values) => {
        values.username = this.props.username
        axios.put(
            this.globalState.domain + '/api/user/set_password',
            values,
            this.globalState.request_headers
        ).then(
            () => {
                if (this.props.logOut){
                    logOut();
                }else{
                    message.success('success');
                }
            }
        ).catch(
            e => {
                if (e.response && e.response.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail);
                }
            }
        )
    }

    render() {
        return (
            <div>
                <Form
                    ref={ref => this.formRef = ref}
                    layout="vertical"
                    onFinish={this.OnFinished}
                >
                    <Form.Item
                        name="old_pwd"
                        label="Old Password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your old password!',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password placeholder="password"/>
                    </Form.Item>

                    <Form.Item
                        name="pwd1"
                        label="New password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your new password!',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password placeholder="password"/>
                    </Form.Item>

                    <Form.Item
                        name="pwd2"
                        label="Confirm Password"
                        dependencies={['pwd']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: 'Please confirm your password!',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('pwd1') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                },
                            }),
                        ]}
                    >
                        <Input.Password placeholder="confirm password"/>
                    </Form.Item>
                </Form>
            </div>
        );
    }
}

export class AboutMe extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState()
        this.state = {
            data: {},
            user_profile: {},
            api_keys: {},
            bandwidth_quota: {},
            capacity_quota: {},

            visible: false,
            drawer_content: null,
        }

        this.Child = React.createRef()

        this.style = {
            itemField: {
                width: '200px',
                display: "inline-block"
            }
        }
    }

    componentDidMount() {
        this.getUserDetail()
    }

    handleQuota = (e) => {
        let text = e.target.innerText;
        // eslint-disable-next-line default-case
        switch (text) {
            case 'Purchase storage capacity':
                this.setState({
                    visible: true,
                    drawer_content: <CapacityQuota
                        onFinish={this.capacityQuotaOnFinished}
                        ref={ref => this.Child = ref}
                    />,
                    drawer_title: 'Purchase Storage Capacity'
                })
                break;

            case 'Purchase bandwidth':
                this.setState({
                    visible: true,
                    drawer_content: <BandwidthQuota
                        onFinish={this.bandwidthQuotaOnFinished}
                        ref={ref => this.Child = ref}
                    />,
                    drawer_title: 'Purchase Download Bandwidth'
                })
                break;
        }

    }

    handleChangeKeys = () => {
        axios.put(
            this.globalState.domain + '/api/user/keys',
            {
                allow_ip: '0.0.0.0'
            },
            this.globalState.request_headers
        ).then(
            () => this.getUserDetail()
        ).catch(
            e => {
                if (e.response.status === 403) {
                    window.sessionStorage.setItem('user_info', null)
                }
            }
        )
    }

    onSubmit = () => {
        this.Child.submit()
    }

    capacityQuotaOnFinished = (values) => {
        axios.post(
            this.globalState.domain + '/api/user/quota/storage',
            values,
            this.globalState.request_headers
        ).then(
            () => message.success('success')
        ).catch(
            e => {
                if (e.response && e.response.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail);
                }
            }
        )
    }

    bandwidthQuotaOnFinished = (values) => {
        axios.post(
            this.globalState.domain + '/api/user/quota/bandwidth',
            values,
            this.globalState.request_headers
        ).then(
            () => message.success('success')
        ).catch(
            e => {
                if (e.response && e.response.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail);
                }
            }
        )
    }

    handleChangePassord = () => {
        this.setState({
            visible: true,
            drawer_content: <ChangePassword
                username={this.globalState.user_info.data.username}
                ref={ref => this.Child = ref}
                logOut={true}
            />,
            drawer_title: 'change password'
        })
        // this.Child.submit();
    }

    closeDrawer = () => {
        this.setState({
            visible: false,
        })
        this.getUserDetail();
    }

    getUserDetail = () => {
        axios.get(
            this.globalState.domain + '/api/user/detail', {
                params:{
                    user_id: this.globalState.user_info.data.user_id
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret => {
            this.setState({
                data: ret.data.data,
                user_profile: ret.data.data.profile,
                api_keys: ret.data.data.keys,
                bandwidth_quota: ret.data.data.bandwidth_quota,
                capacity_quota: ret.data.data.capacity_quota,
            })
        })
    }

    render() {
        return (
            <Card title={'About Me'}>
                <Tabs defaultActiveKey="1">
                    <TabPane tab="Basic" key="1">
                        <Card>
                            <p>
                                <b style={this.style.itemField}>Account Balance</b>
                                {this.state.user_profile.balance}
                                <Button type={"link"} onClick={
                                    () => Modal.info({
                                        title: "recharge",
                                        content: <Recharge />,
                                    })
                                }>recharge</Button>
                            </p>
                            <p>
                                <b style={this.style.itemField}>UserName</b>
                                {this.state.data.username}
                            </p>
                            <p>
                                <b style={this.style.itemField}>Name</b>
                                {this.state.data.first_name}
                            </p>
                            <p>
                                <b style={this.style.itemField}>Mail</b>
                                {this.state.data.email}
                            </p>
                            <p>
                                <b style={this.style.itemField}>
                                    User Class
                                </b>
                                {this.state.data.is_superuser ? "SuperUser" : "Normal"}
                            </p>
                            <p>
                                <b style={this.style.itemField}>User Type</b>
                                {this.state.user_profile.is_subuser ? "Sub User" : "Normal"}
                            </p>
                            <p>
                                <b style={this.style.itemField}>Mail</b>
                                {this.state.user_profile.phone}
                            </p>
                            <p>
                                <Button type={"link"} style={{padding: '0'}} onClick={this.handleChangePassord}>
                                    change password
                                </Button>
                            </p>
                        </Card>
                    </TabPane>
                    <TabPane tab="Quota" key="2">
                        <Card>
                            <p>
                                <b style={this.style.itemField}>Bandwidth</b>
                                {this.state.bandwidth_quota.bandwidth}M
                            </p>
                            <p>
                                <b style={this.style.itemField}>Status</b>
                                {this.state.bandwidth_quota.expired ? (
                                    <Tooltip title="restore to default bandwidth">
                                        <span style={{color: "#e00"}}>Expired</span>&nbsp;<QuestionCircleOutlined/>
                                    </Tooltip>
                                ) : 'Normal'}
                            </p>
                            <p>
                                <b style={this.style.itemField}>End time</b>
                                {this.state.bandwidth_quota.deadline ? this.state.bandwidth_quota.deadline.substr(0, 16) : ''}
                            </p>
                            <p>

                                <Button
                                    type={"link"}
                                    style={{padding: '0'}}
                                    onClick={this.handleQuota}
                                >
                                    Purchase bandwidth
                                </Button>
                            </p>
                            <Divider/>
                            <p><
                                b style={this.style.itemField}>Capacity</b>
                                {this.state.capacity_quota.capacity}G
                            </p>
                            <p>
                                <b style={this.style.itemField}>Status</b>
                                {this.state.capacity_quota.expired ?
                                    <span style={{color: "#e00"}}>Expired</span> : 'Normal'}
                            </p>
                            <p>
                                <b style={this.style.itemField}>End time</b>
                                {this.state.capacity_quota.deadline ? this.state.capacity_quota.deadline.substr(0, 16) : ''}
                            </p>
                            <p>
                                <Button type={"link"} style={{padding: '0'}} onClick={this.handleQuota}>
                                    Purchase storage capacity
                                </Button>
                            </p>
                        </Card>
                    </TabPane>

                    <TabPane tab="API Key" key="3">
                        <Card>
                            <p>
                                <b style={this.style.itemField}>Access Key</b>
                                {this.state.api_keys.user_access_key}
                            </p>
                            <p>
                                <b style={this.style.itemField}>Secret Key</b>
                                {this.state.api_keys.user_secret_key}
                            </p>
                            <p>
                                <b style={this.style.itemField}>Server</b>
                                {this.globalState.domain}
                            </p>
                            <p>
                                <Button type={"link"} style={{padding: '0'}} onClick={this.handleChangeKeys}>
                                    change keys
                                </Button>
                            </p>
                        </Card>
                    </TabPane>
                </Tabs>
                <Drawer
                    visible={this.state.visible}
                    width="500px"
                    title={this.state.drawer_title}
                    placement="right"
                    closable={true}
                    onClose={this.closeDrawer}
                    footer={
                        (<div style={{textAlign: 'right'}}>
                            <Button onClick={this.onSubmit} type="primary">
                                Submit
                            </Button>
                        </div>)
                    }
                >
                    {this.state.drawer_content}
                </Drawer>
            </Card>
        );
    }
}
