import React, {Component} from "react";
import {Breadcrumb, Layout, Statistic, Row, Col, Tabs, Card,} from 'antd';
import {Liquid, Pie, Column} from '@ant-design/charts';

import {formatBytes} from "./funs";
import store from "../store";
import axios from "axios";

const Content = Layout
const {TabPane} = Tabs;

class ClusterUsage extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            pie_data: [],
            percent: 0,
            used_total: 0,
        }
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return nextState !== this.state;
    }

    componentDidMount() {
        this.getClusterUsage();
    }

    getClusterUsage = () => {
        axios.get(
            this.globalState.domain + '/api/user/cluster_usage', {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret => {
                this.setState({
                    pie_data: ret.data.data,
                    percent: ret.data.allocate_total / ret.data.license_capacity,
                    used_total: ret.data.used_total,
                })
            }
        )

    }

    liquidConfig = {
        // percent: 0.25,
        height: 200,
        outline: {
            border: 4,
            distance: 10,
        },
        wave: {length: 128},
    };

    render() {
        return (
            <Row gutter={10} style={{marginTop: 10}}>
                <Col span={8}>
                    <Card title={'Unallocated'}>
                        <Liquid {...this.liquidConfig} percent={this.state.percent}/>
                    </Card>
                </Col>

                <Col span={8}>
                    <Card title={'Bucket distribution'}>
                        <Pie
                            height={200}
                            angleField={'bucket_total'}
                            colorField={'region_name'}
                            data={this.state.pie_data}
                            label={
                                {
                                    type: 'inner',
                                    offset: '-30%',
                                    content: function content(_ref) {
                                        var percent = _ref.percent;
                                        return ''.concat((percent * 100).toFixed(0), '%');
                                    },
                                    style: {
                                        fontSize: 14,
                                        textAlign: 'center',
                                    },
                                }
                            }
                            interactions={[{type: 'element-active'}]}
                        />
                    </Card>
                </Col>

                <Col span={8}>
                    <Card title={'Real used'}>
                        <Pie
                            height={200}
                            angleField={'file_size_total'}
                            colorField={'region_name'}
                            innerRadius={0.6}
                            data={this.state.pie_data}
                            label={
                                {
                                    type: 'inner',
                                    offset: '-50%',
                                    content: function content(_ref) {
                                        var percent = _ref.percent;
                                        return ''.concat((percent * 100).toFixed(0), '%');
                                    },
                                    style: {
                                        fontSize: 14,
                                        textAlign: 'center',
                                    },
                                }
                            }
                            statistic= {
                                {
                                    title: false,
                                    content: {
                                        style: {
                                            whiteSpace: 'pre-wrap',
                                            overflow: 'hidden',
                                            textOverflow: 'ellipsis',
                                        },
                                        content: this.state.used_total+'G',
                                    },
                                }
                            }
                            interactions={[{type: 'element-active'}]}
                        />
                    </Card>
                </Col>
            </Row>
        )
    }
}

export class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            summary_data: {
                capacity: 0,
                c_expired: false,
                bandwidth: 0,
                b_expired: false,
                balance: 0,
                buckets: 0
            },
            usage_times: [],
            usage_bytes: []
        }
    }

    buildLastWeekDate = () => {
        let nowdate = new Date();
        let oneweekdate = new Date(nowdate - 86400 * 30 * 1000);
        let y = oneweekdate.getFullYear();
        let m = oneweekdate.getMonth() + 1;
        let d = oneweekdate.getDate();
        if (m >= 1 && m <= 9) {
            m = '0' + m
        }
        return y + '-' + m + '-' + d
    }

    componentDidMount() {
        let lastweek = this.buildLastWeekDate();
        // console.log(lastweek)
        let userId = this.globalState.user_info.data.user_id;
        let userName = this.globalState.user_info.data.username;
        let user_detail = axios.get(
            this.globalState.domain + '/api/user/detail', {
                params: {
                    user_id: userId,
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => {
            this.setState({
                    summary_data: {
                        capacity: ret.data.data.capacity_quota.capacity,
                        c_expired: ret.data.data.capacity_quota.expired,
                        bandwidth: ret.data.data.bandwidth_quota.bandwidth,
                        b_expired: ret.data.data.bandwidth_quota.expired,
                        balance: ret.data.data.profile.balance,
                        buckets: ret.data.data.bucket.length,
                    },
                }
            )
        })

        let user_usage = axios.get(
            this.globalState.domain + '/api/user/usage', {
                params:{
                    username: userName,
                    start: lastweek
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => {
            // console.log(ret.data.data)
            let usage_times_data = []
            let usage_bytes_data = []
            ret.data.data.map((item) => {
                usage_times_data.push({
                    date: item.date,
                    value: item.usage.put_obj.successful_ops,
                    category: 'put'
                })

                usage_times_data.push({
                    date: item.date,
                    value: item.usage.get_obj.successful_ops,
                    category: 'get'
                })

                usage_bytes_data.push({
                    date: item.date,
                    value: item.usage.put_obj.bytes_received,
                    category: 'put'
                })

                usage_bytes_data.push({
                    date: item.date,
                    value: item.usage.get_obj.bytes_sent,
                    category: 'get'
                })

                return false;
            })

            this.setState({
                usage_times: usage_times_data,
                usage_bytes: usage_bytes_data,
            })
        })

        Promise.all([user_detail, user_usage]).catch(e => {
            if (e.response && e.response.data.status === 403) {
                window.sessionStorage.setItem('user_info', null);
                window.location.reload();
            }
        })
    }

    render() {
        return (
            <Content style={{padding: '0 50px'}}>
                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                    <Breadcrumb.Item>Summary</Breadcrumb.Item>
                </Breadcrumb>
                <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                    <Content style={{minHeight: 280}}>
                        <div style={
                            {
                                borderLeft: '5px solid #0055af',
                                height: '50px',
                                lineHeight: '50px',
                                fontSize: '14px',
                                marginBottom: '10px',
                                paddingLeft: '10px',
                                background: 'white'
                            }
                        }>
                            {`hello ${this.globalState.user_info.data.first_name}, welcome login`}
                        </div>
                        <Row gutter={[10, 10]}>
                            <Col span={6}>
                                <Card>
                                    <Statistic
                                        title="Buckets"
                                        value={this.state.summary_data.buckets}/>
                                </Card>

                            </Col>
                            <Col span={6}>
                                <Card>
                                    <Statistic
                                        title="Account Balance (CNY)"
                                        value={this.state.summary_data.balance}
                                        precision={2}/>
                                    {/*<Button style={{ marginTop: 16 }} type="primary">*/}
                                    {/*Recharge*/}
                                    {/*</Button>*/}
                                </Card>
                            </Col>
                            <Col span={6}>
                                <Card>
                                    <Statistic
                                        title="User Capacity"
                                        value={this.state.summary_data.capacity}
                                        valueStyle={{color: this.state.summary_data.c_expired ? '#cf1322' : '#000000'}}
                                        suffix={"G"}/>
                                </Card>

                            </Col>

                            <Col span={6}>
                                <Card>
                                    <Statistic
                                        title="Bandwidth"
                                        value={this.state.summary_data.bandwidth}
                                        valueStyle={{color: this.state.summary_data.b_expired ? '#cf1322' : '#000000'}}
                                        suffix={"M"}/>
                                </Card>
                            </Col>
                        </Row>

                        {
                            this.globalState.user_info.data.user_type === 'superuser' ? (<ClusterUsage/>) : null
                        }

                        <Card title={"Frequency of used"} style={{marginTop: 10}}>
                            <Tabs defaultActiveKey="1">
                                <TabPane tab="Times" key="1">
                                    <Column
                                        height={300}
                                        xField={'date'}
                                        yField={'value'}
                                        smooth={true}
                                        seriesField={'category'}
                                        point={{
                                            size: 5, shape: 'diamond'
                                        }}
                                        data={this.state.usage_times}/>
                                </TabPane>

                                <TabPane tab="Bytes" key="2">
                                    <Column
                                        height={300}
                                        xField={'date'}
                                        yField={'value'}
                                        seriesField={'category'}
                                        smooth={true}
                                        point={{
                                            size: 5, shape: 'diamond'
                                        }}
                                        yAxis={
                                            {
                                                label: {
                                                    formatter: (v) => formatBytes(v)
                                                },
                                            }
                                        }
                                        tooltip={{
                                            formatter: (datum) => {
                                                return { name: datum.category, value: formatBytes(datum.value) };
                                            }
                                        }}
                                        data={this.state.usage_bytes}/>
                                </TabPane>
                            </Tabs>
                        </Card>
                    </Content>
                </Layout>
            </Content>
        )
    }
}
