import {Button, Input, Table, Form, Select, message} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";
import {UserOutlined} from "@ant-design/icons";

export class Authorize extends React.Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            dataSource:[],
        }
        this.urls = {
            object:{
                get: this.globalState.domain+'/api/objects/acl?obj_id=',
                delete: this.globalState.domain+'/api/objects/acl?acl_oid=',
                add: this.globalState.domain+'/api/objects/acl'
            },
            bucket: {
                get: this.globalState.domain+'/api/buckets/acl?bucket_id=',
                delete: this.globalState.domain+'/api/buckets/acl?acl_bid=',
                add: this.globalState.domain+'/api/buckets/acl'
            }
        }
        this.url = null;
        if (this.props.type === 'bucket'){
            this.url = this.urls.bucket
        }

        if (this.props.type === 'object'){
            this.url = this.urls.object
        }

        this.queryAuthorizedList(props.id)
    }

    queryAuthorizedList = (id) => {
        axios.get(
            this.url.get+id,
            this.globalState.request_headers
        ).then((res) => {
            this.setState({
                dataSource:res.data.data
            })
        })
    }

    handleDeleteAuthorize = (e)=>{
        axios.delete(
            this.url.delete+e.target.id,
            this.globalState.request_headers
        ).then((res)=>{
            message.info('success')
            this.queryAuthorizedList(this.props.id)
        }).catch((e)=>{
            message.warning(e.response.data.detail)
        })
    }

    onSubmit = (data) => {
        if (this.props.type === 'object'){
            data['obj_id'] = parseInt(this.props.id);
        }else{
            data['bucket_id'] = parseInt(this.props.id);
        }
        axios.post(
            this.url.add,
            data,
            this.globalState.request_headers
        ).then((res)=>{
            message.info('success');
            this.queryAuthorizedList(this.props.id)
        }).catch((e)=>{
            message.warning(e.response.data.detail)
        })
    }

    columns = [
        {
            title: 'User',
            dataIndex: 'user__username',
        },
        {
            title: this.props.type === 'bucket'?'Bucket':'Object',
            dataIndex: this.props.type === 'bucket'?'bucket__name':'object__name',
        },{
            title: 'Permission',
            dataIndex: 'permission',
        },{
            title: 'Operate',
            dataIndex: 'acl_bid',
            render: (id, value)=>{
                return (
                    // eslint-disable-next-line jsx-a11y/anchor-is-valid
                    <a
                        href="#" id={value.acl_bid !== undefined ? value.acl_bid : value.acl_oid}
                        onClick={this.handleDeleteAuthorize}
                    >Remove</a>
                )
            }
        },
    ];

    render() {
        return (
            <React.Fragment>
                <p>New authorize</p>
                <Form layout="inline" onFinish={this.onSubmit} initialValues={{
                    permission: 'authenticated-read',
                }}>
                    <Form.Item name="username" rules={[
                        {
                            required:true,
                            message: 'please input authorize username'
                        }
                    ]}>
                        <Input maxLength={64} style={{width:"200px"}} prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" />
                    </Form.Item>
                    <Form.Item name="permission" rules={[
                        {
                            required: true,
                            message: 'please choice permission'
                        }
                    ]}>
                        <Select style={{width:"100px"}}>
                            <Select.Option value="authenticated-read">read</Select.Option>
                            <Select.Option value="authenticated-read-write">read-write</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button htmlType="submit" type="primary">Submit</Button>
                    </Form.Item>
                </Form>

                <p style={{margin: '30px 0 10px 0'}}>Authorized list</p>
                <Table dataSource={this.state.dataSource} pagination={false} columns={this.columns} rowKey={record=>record.acl_bid !== undefined ? record.acl_bid : record.acl_oid} />
            </React.Fragment>
        )
    }
}