class NewRole extends Component {
    constructor(props) {
        super(props);
        this.formRef = React.createRef();
    }

    submit = () => {
        this.formRef.submit()
    }

    render() {
        return (
            <Form layout={'vertical'} ref={ref => this.formRef = ref} onFinish={this.props.onFinish}>
                <Form.Item
                    name={'name'}
                    label={'name'}
                    rules={[
                        {
                            required: true,
                            message: 'please input role name',
                            max: 20,
                        }
                    ]}>
                    <Input placeholder={'role name'}/>
                </Form.Item>
            </Form>
        )
    }

}

class AddPermission extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.targetKeys = [];
        this.state = {
            data: [],
        }
    }

    getAllPerms = () => {
        axios.get(
            this.globalState.domain + '/api/user/perm/all_perms',
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(ret => {
            // console.log(ret)
            this.setState({
                // defaultSelect: perms,
                data: ret.data.data
            })
        }).catch(e => {
            if (e.response && e.response.status === 403) {
                window.sessionStorage.setItem('user_info', null);
                window.location.reload();
            } else {
                message.warning(e.response.data.detail)
            }
        })
    }

    componentDidMount() {
        this.getAllPerms();
    }

    submit = () => {
        if (this.props.roleName === undefined) {
            message.warning('role name is empty')
            return
        }
        axios.post(
            this.globalState.domain + '/api/user/group/perm',
            {
                role: this.props.roleName,
                perms: this.targetKeys
            },
            this.globalState.request_headers
        ).then(ret => message.success('success')).catch(
            e => {
                if (e.response && e.response.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.error(e.response.data.detail);
                }
            }
        )
    }

    onCheck = (e) => {
        let data = []
        e.map((item) => {
            if (item.indexOf('-') === -1) {
                data.push(item)
            }
            return false;
        })
        this.targetKeys = data
    }

    render() {
        if (this.state.data.length === 0) {
            return 'loading tree'
        } else {
            return (
                <Tree
                    checkable
                    defaultCheckedKeys={this.props.hasPermissions.map((item) => item.key)}
                    // onSelect={this.onSelect}
                    onCheck={this.onCheck}
                    treeData={this.state.data}
                />
            )
        }


    }

}


export class Roles extends Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.Child = React.createRef();
        this.state = {
            data: [],
            drawer_title: '',
            visisble: false,
            drawer_content: ''
        }
    }

    componentDidMount() {
        this.getAllRoles();
    }

    setDefaultRole = (id) => {
        axios.put(
            this.globalState.domain + '/api/user/role/default',
            {
                group_id: id
            },
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            () => {
                message.success('success');
                this.getAllRoles();
            }
        ).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail)
                }
            }
        )
    }

    removeRole = (id) => {
        axios.delete(
            this.globalState.domain + '/api/user/group/role',
            {
                params:{
                    id: id
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(
            () => {
                message.success('success');
                this.getAllRoles();
            }
        ).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail)
                }
            }
        )
    }

    removePermission = (p, roleName) => {
        axios.delete(
            this.globalState.domain + '/api/user/group/perm',
            {
                data: {
                    role: roleName,
                    perms: [p]
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(
            () => message.success('success')
        ).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail)
                }
            }
        )
    }

    UsersInRole = (data, roleName) => {
        return data.map(
            (item, index) => (
                <Tag
                    style={{marginBottom: 10}}
                    key={'tag_index_' + index}
                    closable
                    onClose={
                        () => this.removePermission(item.key, roleName)
                    }
                >
                    {item.label}
                </Tag>
            )
        )
    }

    getAllRoles = () => {
        axios.get(
            this.globalState.domain + '/api/user/group/role', {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret => this.setState({
                data: ret.data.data
            })
        )
    }

    handleAddRole = (values) => {
        axios.post(
            this.globalState.domain + '/api/user/group/role',
            values,
            this.globalState.request_headers
        ).then(
            () => {
                message.success('success');
                // this.props.onSuccess();
                this.subComponentSuccess();
            }
        ).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                } else {
                    message.warning(e.response.data.detail);
                }
            }
        )
    }
    handleBtnClick = (text, record) => {
        // console.log(record)
        // let text = e.target.innerText;
        // eslint-disable-next-line default-case
        switch (text) {
            case 'add permission':
                this.setState({
                    drawer_title: 'add permission to role',
                    drawer_content: <AddPermission hasPermissions={record.permissions} roleName={record.name}
                                                   ref={ref => this.Child = ref}/>,
                    visible: true,
                })
                break;
            case 'set default role':
                this.setDefaultRole(record.id);
                break;
            case 'delete':
                Modal.confirm({
                    icon: <ExclamationCircleOutlined/>,
                    title: "warning",
                    okType: 'danger',
                    content: 'Deleting roleall associated users will lose their permissions',
                    onOk: () => {
                        this.removeRole(record.id);
                    },
                });
                break;
            case 'New Role':
                this.setState({
                    drawer_title: 'new role',
                    drawer_content: <NewRole ref={ref => this.Child = ref} onFinish={this.handleAddRole}/>,
                    visible: true,
                })
                break;
        }
    }

    drawerClose = (e) => {
        this.setState({
            drawer_title: null,
            drawer_content: null,
            visible: false
        })
        // this.getAllRoles();
    }

    subComponentSuccess=()=>{
        this.setState({
            drawer_title: null,
            drawer_content: null,
            visible: false
        })
        this.getAllRoles();
    }

    onSubmit = () => {
        this.Child.submit();
    }

    regionColumns = [
        {
            title: 'name',
            dataIndex: 'name',
        },
        {
            title: 'default',
            dataIndex: 'default',
            width: 150,
            render: (v) => {
                return v ? 'Yes' : 'No'
            }
        },
        {
            title: 'action',
            dataIndex: 'id',
            width: 200,
            render: (v, record) => {
                // console.log(record)
                return (
                    <div>
                        {/*<Button type={'link'}>add user</Button>*/}
                        <Button type={'link'} onClick={() => this.handleBtnClick('add permission', record)}>add permission</Button>
                        <Button type={'link'} onClick={() => this.handleBtnClick('set default role', record)}>set default role</Button>
                        <Button type={'link'} onClick={() => this.handleBtnClick('delete', record)}>delete</Button>
                    </div>
                )
            }
        }
    ]

    render() {
        return (
            <Card title={'Role List'}>
                <Button
                    onClick={()=>this.handleBtnClick('New Role', null)}
                    type={'primary'}
                    style={{marginBottom: 10}}
                >
                    <PlusOutlined/>New Role
                </Button>
                <Table
                    dataSource={this.state.data}
                    columns={this.regionColumns}
                    rowKey={record => record.id}
                    expandable={{
                        expandedRowRender: record =>
                            <div>
                                <b style={{paddingRight: 10}}>permissions</b>
                                {this.UsersInRole(record.permissions, record.name)}
                                <br/>
                                <b>users</b>
                            </div>
                        // rowExpandable: record => record.name !== 'Not Expandable',
                    }}
                />

                <Drawer
                    title={this.state.drawer_title}
                    width={500}
                    onClose={this.drawerClose}
                    visible={this.state.visible}
                    closable={true}
                    footer={(<div style={{textAlign: 'right'}}>
                        <Button onClick={this.onSubmit} type="primary">
                            Submit
                        </Button>
                    </div>)}
                >
                    {this.state.drawer_content}
                </Drawer>
            </Card>
        )
    }
}