import {Alert, message, Select} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";


export class BucketBackup extends React.Component{
    constructor(props) {
        super(props);
        this.globalState=store.getState();
        this.state = {
            bucket_region_id: null,
            region_list: [],
        }
    }

    componentDidMount() {
        this.getRegionList();
    }

    submit=()=>{
        axios.put(
            this.globalState.domain+'/api/buckets/bucket',
            {
                bucket_id: this.props.id,
                bucket_region_id: this.state.bucket_region_id,
            },
            this.globalState.request_headers
        ).then(
            ()=>{
                message.info('success');
                this.props.onSuccess();
            }
        ).catch(
            e=>message.warning(e.response.data.detail)
        )
    }

    getRegionList = () => {
        axios.get(
            `${this.globalState.domain}/api/buckets/region`,
            this.globalState.request_headers
        ).then((res) => {
            this.setState({
                region_list: res.data.data
            })
        }).catch((e) => {
            console.log(e.response)
        })
    }

    renderRegionOption = () => {
        return this.state.region_list.map(item =>
            <Select.Option key={item.reg_id} value={item.reg_id}>{item.name}</Select.Option>);
    }

    handleRegionChange=(e)=>{
        this.setState({
            bucket_region_id:e
        })
    }

    render(){
        return(
            <React.Fragment>
                <Alert showIcon type="info" message={'Notice'} description={<div>
                    <p>1. The backup operation will automatically create a new bucket, creating a backup will cost more storage space </p>
                    <p>2. The new bucket just be read-only, upload or delete files to the original bucket, the backup bucket will automatically synchronize the operation,</p>
                </div>}/>
                <p style={{marginTop:"20px", marginBottom:"10px"}}>Backup Destination Region</p>
                <Select placeholder="region" style={{width: "100%"}} onSelect={this.handleRegionChange}>
                    {this.renderRegionOption()}
                </Select>
            </React.Fragment>

        )
    }

}
