import {Input, message} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";


export class NewFolder extends React.Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            folder_name: '',
        }
    }

    submit = () => {
        if (this.state.folder_name.trim().length === 0){
            message.warning('please input folder name');
            return false;
        }
        axios.post(
            this.globalState.domain + '/api/objects/create_folder',
            this.props.path.length === 0 ?
            {
                bucket_name: this.props.name,
                folder_name: this.state.folder_name,
            } : {
                    bucket_name: this.props.name,
                    folder_name: this.state.folder_name,
                    path: this.props.path
                },
            this.globalState.request_headers
        ).then(
            () => {
                message.info('success');
                this.setState({
                    folder_name: ''
                })
                this.props.onSuccess();
            }
        ).catch(
            e => message.warning(e.response.data.detail)
        )

    }

    render() {
        return (
            <Input placeholder={"please input folder name"} onChange={(e) => {
                this.setState({
                    folder_name: e.target.value
                })
            }}/>
        )
    }
}
