import {Radio, message, Alert} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";


export class Permission extends React.Component {
    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.explain = {
            private: 'Only the owner of the bucket can access it',
            public_read_write: 'Danger: Anybody can read and write data to this bucket!!!',
            public_read: 'Anybody can read data from this bucket',
            authenticated: 'Only the owner and authorized person can access it'
        }

        this.urls = {
            bucket: {
                get: this.globalState.domain + '/api/buckets/query_perm?bucket_id=',
                change: this.globalState.domain + '/api/buckets/set_perm',
            },
            object: {
                get: this.globalState.domain + '/api/objects/query_perm?obj_id=',
                change: this.globalState.domain + '/api/objects/set_perm',
            }
        }

        if (this.props.type === 'bucket') {
            this.url = this.urls.bucket
        } else {
            this.url = this.urls.object
        }

        this.state = {
            default: null,
            explain: null,
        }

    }

    componentDidMount() {
        this.queryPermission(this.props.id);
    }

    queryPermission = (id) => {
        axios.get(
            this.url.get + id,
            this.globalState.request_headers
        ).then(res => {
            this.setState({
                default: res.data.permission,
                explain: this.explain[res.data.permission.replaceAll('-', '_')]
            })
        })
    }

    handlePermBtnClick = (e) => {
        this.setState({
            default: e.target.value,
            explain: this.explain[e.target.value.replaceAll('-', '_')]
        })
    }

    submit = () => {
        axios.put(
            this.url.change,
            this.props.type === 'bucket' ? {
                permission: this.state.default,
                bucket_id: this.props.id
            } : {
                permission: this.state.default,
                obj_id: this.props.id
            },
            this.globalState.request_headers
        ).then(
            (res) => {
                message.info("success");
                this.props.onSuccess();
            }
        ).catch((e) => message.warning(e.response.data.detail))
    }

    render() {
        return (
            <React.Fragment>
                <Radio.Group value={this.state.default} buttonStyle="solid" style={{marginTop: 16}}
                             onChange={this.handlePermBtnClick}>
                    <Radio.Button value="private">Private</Radio.Button>
                    <Radio.Button value="public-read">Public-read</Radio.Button>
                    <Radio.Button value="public-read-write">Public-read-write</Radio.Button>
                    <Radio.Button value="authenticated">Authenticated</Radio.Button>
                </Radio.Group>
                <Alert type={'info'} message={'notice'} style={{marginTop: 5}} description={this.state.explain} />
            </React.Fragment>
        )
    }
}