import React, {Component} from "react";
import {
    Breadcrumb, Layout, Menu,
    Table, Space, Dropdown,
    Drawer, Button, message, Modal,
} from "antd";
import {formatBytes} from "./funs";

import {
    DownOutlined, FolderOutlined,
    FileOutlined, CloudUploadOutlined,
    PlusOutlined, LeftOutlined,
    FolderAddOutlined, ExclamationCircleOutlined,
} from "@ant-design/icons";

import store from "../store";
import axios from "axios";

import {Permission} from "./component/Permission";
import {Authorize} from "./component/Authorize";
import {NewBucket} from "./component/NewBucket";
import {BucketDetail} from "./component/BucketDetail";
import {Delete} from "./component/Delete";
import {BucketBackup} from "./component/BucketBackup";
import {NewFolder} from "./component/NewFolder";
import {FileUpload} from "./component/Upload";

const {Content} = Layout


const BucketMoreMenu = (record) => {
    let user = JSON.parse(window.sessionStorage.getItem('user_info')).data.user_id
    return (
        <Menu onClick={k=>record.openDrawer(k, record)}>
            {/*<Menu.Divider/>*/}
            <Menu.Item disabled={user !== record.user.id} key={"BucketAuthorize"}>Authorize</Menu.Item>
            <Menu.Item disabled={user !== record.user.id} key={"BucketBackup"}>Backup</Menu.Item>
            <Menu.Item key={"BucketDetail"}>Detail</Menu.Item>
            <Menu.Item disabled={user !== record.user.id} key={"BucketPermission"}>Permission</Menu.Item>
            <Menu.Divider/>
            <Menu.Item disabled={user !== record.user.id} key={"BucketDelete"} danger>Delete</Menu.Item>
        </Menu>
    );
}

const ObjectMoreMenu = (record) => {
    // console.log(record.key)
    return (
        <Menu onClick={k=>record.openDrawer(k, record)}>
            <Menu.Item disabled={record.type === 'd'} key={"ObjectAuthorize"}>Authorize</Menu.Item>
            <Menu.Item disabled={record.type === 'd'} key={"ObjectPermission"}>Permission</Menu.Item>
            <Menu.Divider/>
            <Menu.Item key={"ObjectDelete"} danger>Delete</Menu.Item>
        </Menu>
    );
}


export class Bucket extends Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.Child = React.createRef();
        this.state = {
            drawer_state: false,
            data: [],

            drawer_content: null,
            drawer_foot: null,

            content_type: 'bucket',
            bucket_name: null,
            previous_path: '',
            current_path: '',
            page_info: {},
        };
    }

    objectTableColumns = [
        {
            title: "Name",
            dataIndex: 'name',
            render: (value, record) => {
                if (record.type === 'd') {
                    return (
                        // eslint-disable-next-line jsx-a11y/anchor-is-valid
                        <a href={'#'}
                            onClick={() => this.getObjectsOfBucket(this.state.bucket_name, record.key_url)}
                        >
                            <FolderOutlined style={{marginRight:5}}/>{value}
                        </a>
                    )
                } else {
                    return (
                        // eslint-disable-next-line jsx-a11y/anchor-is-valid
                        <a href={'#'}
                            onClick={()=>this.download_file(record)}
                        >
                            <FileOutlined style={{marginRight:5}}/>{value}
                        </a>
                    )
                }
            }
        },
        {
            title: 'Filesize',
            dataIndex: 'file_size',
            render: (v)=>formatBytes(v,2)
        },
        {title: 'Permission', dataIndex: 'permission'},
        {title: 'Upload Time', dataIndex: 'upload_time',
            render(value) {
                return value.substr(0, 16);
            }
        },
        {
            title: 'Action',
            key: 'obj_id',
            render: (value) => {
                value.openDrawer = this.openDrawer
                return (
                    <Space size="middle">
                        <Dropdown overlay={ObjectMoreMenu.bind(this, value)} trigger={['click']}>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                more<DownOutlined />
                            </a>
                        </Dropdown>
                    </Space>
                )
            }
        },
    ]

    bucketTableColumns = [
        {
            title: 'Name',
            dataIndex: 'name',
            render:(value, record)=>{
                // console.log(record.user.id, this.globalState.user_info.data.user_id)
                if (record.user.id === this.globalState.user_info.data.user_id){
                    // eslint-disable-next-line jsx-a11y/anchor-is-valid
                    return <a href={'#'} onClick={
                        ()=>this.getObjectsOfBucket(value)
                    }>{value}</a>
                }else{
                    return value
                }

            }
        },
        {
            title: 'Owner',
            dataIndex: "user",
            render(value) {
                return value.first_name
            }
        },
        {
            title: 'Permission',
            dataIndex: 'en_permission',
        },
        // {
        //     title: 'Status',
        //     dataIndex: 'en_status',
        //     render:(value)=>{
        //         return value === 'enable'? <Badge status={'success'} text={'enable'}/> : <Badge status={'error'} text={'disable'}/>
        //     }
        // },
        {
            title: 'Region',
            dataIndex: 'bucket_region',
            render(value) {
                return value.name
            }
        },
        {
            title: 'Version Control',
            dataIndex: 'version_control',
            render(value) {
                return value ? 'Enabled' : 'Disabled'
            }
        },
        {
            title: 'Create time',
            dataIndex: 'create_time',
            render(value) {
                return value.substr(0, 10);
            }
        },
        {
            title: 'Action',
            key: 'bucket_id',
            render: (value) => {
                value.openDrawer = this.openDrawer
                return (
                    <Space size="middle">
                        <Dropdown overlay={BucketMoreMenu.bind(this, value)} trigger={['click']}>
                            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                            <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                more<DownOutlined />
                            </a>
                        </Dropdown>
                    </Space>
                )
            }
        },
    ];

    getObjectsOfBucket = (name = this.state.bucket_name, path = this.state.current_path, page = 1) => {
        if (name === null) return;

        axios.get(
            this.globalState.domain + '/api/objects/list_objects',{
                params:{
                    bucket_name: name,
                    page: page,
                    path: path
                },
                headers: this.globalState.request_headers.headers
            }

        ).then((ret) => {
            this.setState({
                bucket_name: name,
                content_type: 'file',
                data: ret.data.data,
                previous_path: ret.data.page_info.previous_path,
                current_path: ret.data.page_info.current_path,
                page_info:  ret.data.page_info
            })
        }).catch(
            e=>{
                if (e.response && e.response.data.status === 403){
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                }
            }
        )
    }

    getBucketList = (page = 1) => {
        axios.get(
            `${this.globalState.domain}/api/buckets/bucket`,{
                params:{
                    size: 10,
                    page: page
                },
                headers: this.globalState.request_headers.headers
            }).then(
                (ret) => {
                    this.setState({
                        data: ret.data.data,
                        content_type: 'bucket',
                        page_info: ret.data.page_info
                    })
                }
            ).catch(
                (e) => {
                    if (e.response && e.response.data.status === 403) {
                        window.sessionStorage.setItem('user_info', null);
                        window.location.reload();
                    }
                }
            )
    }

    componentDidMount() {
        this.getBucketList();
    }

    handleBackClick = (e)=>{
        this.getObjectsOfBucket(
            this.state.bucket_name,
            this.state.previous_path,
            this.state.page_info.current_page
        )
    }

    handlePageChange = (p)=>{
        if (this.state.content_type === 'bucket'){
            this.getBucketList(p)
        }else{
            this.getObjectsOfBucket(this.state.bucket_name, this.state.current_path, p)
        }
    }

    deleteObject = (key)=>{
        axios.delete(
            this.globalState.domain+'/api/objects/delete',
            {
                params:{
                    bucket_name: this.state.bucket_name,
                    key: key
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(()=>{
            message.info('success');
            this.getObjectsOfBucket(
                this.state.bucket_name,
                this.state.current_path,
                this.state.page_info.current_page
            )
        }).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    openDrawer = (e, record = null) => {

        let content = null;
        let df = false

        switch (e.key) {
            case "BucketPermission":
                content = <Permission onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} id={record.bucket_id} type="bucket"/>;
                df = this.drawerFoot()
                break;
            case "BucketAuthorize":
                content = <Authorize ref={ref => this.Child = ref} id={record.bucket_id} type="bucket"/>;
                break;
            case "ObjectPermission":
                content = <Permission onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} id={record.obj_id} type="object"/>;
                df = this.drawerFoot()
                break;
            case "ObjectAuthorize":
                content = <Authorize onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} id={record.obj_id} type="object"/>;
                break
            case 'BucketDetail':
                content = <BucketDetail bucket={record.name}/>;
                break;
            case "BucketDelete":
                content = <Delete onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} id={record.bucket_id} name={record.name}/>;
                df = this.drawerFoot();
                break;
            case "BucketBackup":
                content = <BucketBackup onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} id={record.bucket_id} />
                df = this.drawerFoot();
                break;
            case 'ObjectDelete':
                Modal.confirm({
                    icon: <ExclamationCircleOutlined />,
                    title: "warning",
                    okType: 'danger',
                    content: 'Deleting file object confirm, can not resume!!!',
                    onOk:()=>{
                        this.deleteObject(record.key_url)
                    },
                });
                break;
            case 'Create Bucket':
                content = <NewBucket onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref}/>;
                df = this.drawerFoot()
                break;
            case 'Upload':
                content = <FileUpload onSuccess={this.getObjectsOfBucket} ref={ref => this.Child = ref} name={this.state.bucket_name} path={this.state.current_path}/>;
                df = null;
                break;
            case "New folder":
                content = <NewFolder onSuccess={this.subComponentSuccess} ref={ref => this.Child = ref} name={this.state.bucket_name} path={this.state.current_path} />
                df = this.drawerFoot();
                break;
            default:
                break;
        }

        if (content === null) {
            return;
        }
        this.setState({
            drawer_state: true,
            drawer_title: e.key,
            drawer_content: content,
            drawer_foot: df,
            dropdown_menu_state: false
        })
    }

    closeDrawer = () => {
        this.setState({
            drawer_state: false,
            drawer_content: null
        })

    }

    subComponentSuccess=()=>{
        this.closeDrawer();
        if (this.state.content_type === 'bucket'){
            this.getBucketList();
        }else{
            this.getObjectsOfBucket(
                this.state.bucket_name,
                this.state.current_path,
                this.state.page_info.current_page)
        }
    }

    onSubmit = () => {
        this.Child.submit();
    }

    drawerFoot = () => {
        return (
            <div style={{textAlign: 'right'}}>
                <Button onClick={this.onSubmit} type="primary">
                    Submit
                </Button>
            </div>
        )
    }

    download_file = (record)=>{
        axios.post(
            this.globalState.domain+'/api/objects/generate_download_url',
            {
                bucket_name: record.bucket.name,
                key: record.key_url
            },
            this.globalState.request_headers
        ).then(
            r=>{
                window.open(r.data.url, '_blank')
            }
        ).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    render() {
        return (
            <React.Fragment>
                <Content style={{padding: '0 50px'}}>
                    <Breadcrumb style={{margin: '16px 0'}}>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>Bucket</Breadcrumb.Item>
                        <Breadcrumb.Item>List</Breadcrumb.Item>
                    </Breadcrumb>
                    <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                        <Content>
                            <Button
                                type="primary"
                                style={{margin: "10px 0",width:"146px"}}
                                onClick={
                                    ()=>{
                                        let kw = this.state.content_type === 'bucket' ? 'Create Bucket' : 'Upload';
                                        this.openDrawer(
                                            {key: kw}, null
                                        )
                                    }
                                }
                            >
                                {this.state.content_type === 'bucket' ? (<PlusOutlined />) : (<CloudUploadOutlined />)}
                                {this.state.content_type === 'bucket' ? "Create Bucket" : "Upload"}
                            </Button>

                            <p>
                                {
                                    this.state.content_type !== 'bucket' ? (
                                        <React.Fragment>
                                            {
                                                this.state.current_path.length === 0 ? (
                                                    <Button
                                                        type={"link"}
                                                        onClick={()=>this.getBucketList(1)}
                                                        style={{color:"#666"}}
                                                    >
                                                        <LeftOutlined /> Back
                                                    </Button>
                                                ) : (
                                                    <Button type="link" onClick={this.handleBackClick}>
                                                        <LeftOutlined /> Back
                                                    </Button>
                                                )
                                            }
                                                <Button type="link" onClick={()=>this.openDrawer({key: 'New folder'}, null)}>
                                                    <FolderAddOutlined /> New folder
                                                </Button>
                                        </React.Fragment>
                                    ) : (<br/>)
                                }
                            </p>
                            <Table
                                columns={
                                    this.state.content_type === 'bucket' ?
                                    this.bucketTableColumns : this.objectTableColumns
                                }
                                dataSource={this.state.data}
                                pagination={
                                    {
                                        current: this.state.page_info.current_page,
                                        pageSize: this.state.page_info.page_size,
                                        total: this.state.page_info.record_count,
                                        onChange: this.handlePageChange,
                                    }
                                }
                                style={{marginTop:-9}}
                                rowKey={
                                    record => this.state.content_type === 'bucket' ?
                                        record.bucket_id : record.obj_id
                                }
                            />
                        </Content>
                        <Drawer
                            width="500px"
                            title={this.state.drawer_title}
                            placement="right"
                            closable={true}
                            onClose={this.closeDrawer}
                            visible={this.state.drawer_state}
                            footer={this.state.drawer_foot}
                        >
                            {this.state.drawer_content}
                        </Drawer>
                    </Layout>
                </Content>
            </React.Fragment>

        )
    }
}
