import React, {Component} from "react";
import {
    Table, Button, message, Card,
    Drawer, Form, Input, Modal,
    Row, Col, Menu,
    Dropdown, Alert, Collapse
} from "antd";
import {DownOutlined, ExclamationCircleOutlined, UserAddOutlined} from "@ant-design/icons";

import store from "../../store";
import axios from "axios";
import {ChangePassword} from "./AboutMe";
import {formatBytes} from "../funs";

const styles={
    field:{
        display: 'inline-block',
        width: 100,
        textAlign: 'right',
        marginRight: 20,
    }
}

const UserMoreMenu = (record) => {
    return (
        <Menu onClick={(k)=>record.moreMenuEvent(k, record)}>
            <Menu.Item key={"user_detail"}>Detail</Menu.Item>
            <Menu.Item key={"set_password"}>Password</Menu.Item>
            <Menu.Divider/>
            <Menu.Item key={"delete_user"} danger>Delete</Menu.Item>
        </Menu>
    );
}

class UserDetail extends React.Component{
    constructor(props) {
        super(props);
        this.globalState=store.getState();
        this.state={
            basic:{},
            bandwidth:{},
            capacity:{},
            profile:{},
            keys:{}
        }
    }

    componentDidMount() {
        this.getUserDetail(this.props.userId)
    }

    getUserDetail = (userId)=>{
        axios.get(
            this.globalState.domain+'/api/user/detail', {
                params:{
                    user_id: userId
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret=>{
                let data = ret.data.data;
                console.log(data)
                this.setState(
                    {
                        basic: data,
                        profile: data.profile,
                        bandwidth: data.bandwidth_quota,
                        capacity: data.capacity_quota,
                        keys: data.keys,
                    }
                )
            }
        )
    }

    render() {
        return (
            <Collapse defaultActiveKey={['1']}>
                <Collapse.Panel header="Basic" key="1">
                    <p><b style={styles.field}>balance</b>{this.state.profile.balance}</p>
                    <p><b style={styles.field}>username</b>{this.state.basic.username}</p>
                    <p><b style={styles.field}>name</b>{this.state.basic.first_name}</p>
                    <p><b style={styles.field}>email</b>{this.state.basic.email}</p>
                    <p><b style={styles.field}>phone</b>{this.state.profile.phone}</p>
                    <p><b style={styles.field}>real used</b>{formatBytes(this.state.basic.user_used)}</p>
                </Collapse.Panel>
                <Collapse.Panel header="Quota" key="2">
                    <p><b style={styles.field}>bandwidth</b>{this.state.bandwidth.bandwidth}M</p>
                    <p><b style={styles.field}>expire</b>{this.state.bandwidth.expired ? 'Expired' : 'Normal'}</p>
                    <br/>
                    <p><b style={styles.field}>storage</b>{this.state.capacity.capacity}G</p>
                    <p><b style={styles.field}>expire</b>{this.state.capacity.expired ? 'Expired' : 'Normal'}</p>
                </Collapse.Panel>
                <Collapse.Panel header="Keys" key="3">
                    <p><b style={styles.field}>Ceph Uid</b>{this.state.keys.ceph_uid}</p>
                    {/*<p><b>AK</b><br/>{this.state.keys.user_access_key}</p>*/}
                    {/*<p><b>SK</b><br/>{this.state.keys.user_secret_key}</p>*/}
                </Collapse.Panel>
            </Collapse>
        )
    }
}

export class Users extends Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.drawerRef=React.createRef()
        this.state = {
            user_list: [],
            current_page: 1,
            page_size: 10,
            rs_count: 1,
            visible: false,
            drawer_content: null,
            drawer_title: null,
            drawer_foot: null,
        }
        this.url = `${this.globalState.domain}/api/user/list_user?page=${this.state.current_page}&size=${this.state.page_size}`;
        if (this.props.type === 'subuser') {
            this.url = this.url + '&subuser=1'
        }

    }

    componentDidMount() {
        this.getUserList();
        if (this.props.type === 'superuser'){
            this.userColumns.splice(4,0,
                {
                    title: 'Storage Quota',
                    dataIndex: 'capacity_quota.capacity',
                    render: (value, record) => {
                        // console.log(record)
                        return  record.capacity_quota.capacity === 0 ?
                            '0' : record.capacity_quota.capacity+'G'
                    }
                }
            )
        }
    }

    userColumns = [
        {
            title: 'UserName',
            dataIndex: 'username'
        },
        {
            title: 'Name',
            dataIndex: 'first_name',
        },
        {
            title: 'Telephone',
            dataIndex: 'profile.phone',
            render: (value, record) => {
                return record.profile.phone
            }
        },
        {
            title: 'Phone Validated',
            dataIndex: 'profile.phone_verify',
            render: (value, record) => {
                return record.profile.phone_verify ? 'Yes' : 'No'
            }
        },
        {
            title: 'Date joined',
            dataIndex: 'date_joined',
            render: (value, record) => {
                return value.substr(0, 16);
            }
        },
        {
            title: 'Last login',
            dataIndex: 'last_login',
            render: (value, record) => {
                return value ? value.substr(0, 16) : '';
            }
        },
        {
            title: 'Action',
            dataIndex: 'id',
            render: (value, record) => {
                record.moreMenuEvent=this.handleMoreMenuEvent;
                return (
                    <React.Fragment>
                        {this.props.type === this.globalState.user_info.data.user_type
                            ? <Dropdown overlay={UserMoreMenu.bind(this, record)} trigger={['click']}>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                    more<DownOutlined />
                                </a>
                            </Dropdown>
                            : <Button type={'link'} onClick={() => this.handleBtnRemoveUser(record)}>Delete</Button>
                        }

                    </React.Fragment>
                )
            }
        }
    ]

    getUserList = (page = 1, username=undefined) => {
        if (username !== undefined) {
            this.url += '&username=' + username
        }
        axios.get(
            this.url,
            {
                headers: this.globalState.request_headers.headers
            }
        ).then(
            ret => this.setState({
                user_list: ret.data.data,
                rs_count: ret.data.page_info.record_count,
                current_page: ret.data.page_info.current_page
            })
        ).catch(e => {
            if (e.response && e.response.status === 403) {
                window.sessionStorage.setItem('user_info', null);
            }
        })
    }

    removeUser = (record) => {
        axios.delete(
            this.globalState.domain + '/api/user/delete',
            {
                data: {
                    username: record.username,
                    user_id: record.id
                },
                headers: this.globalState.request_headers.headers
            }
        ).then(() => {
            message.success('success');
            this.getUserList();
        }).catch(
            e => {
                if (e.response && e.response.data.status === 403) {
                    window.sessionStorage.setItem('user_info', null);
                    window.location.reload();
                }else{
                    message.warning(e.response.data.detail)
                }
            }
        )
    }

    handleBtnRemoveUser = (record) =>{
        Modal.confirm({
            icon: <ExclamationCircleOutlined />,
            title: "warning",
            okType: 'danger',
            content: 'Deleting user will be destroy all related data',
            onOk:()=>{
                this.removeUser(record)
            },
        })
    }

    handlePageChange = (p) => {
        this.getUserList(p)
    }

    handleMoreMenuEvent=(e, record)=>{
        // console.log(e, record)
        // eslint-disable-next-line default-case
        switch (e.key){
            case 'delete_user':
                this.handleBtnRemoveUser(record)
                break;
            case 'set_password':
                this.setState({
                    visible:true,
                    drawer_title: 'Setting password',
                    drawer_foot: this.drawer_footer(),
                    drawer_content: <ChangePassword username={record.username} ref={ref=>this.drawerRef=ref} />
                })
                break;
            case 'user_detail':
                this.setState({
                    visible: true,
                    drawer_title: 'User Detail Information',
                    drawer_content: <UserDetail userId={record.id}/>,
                })
                break;
        }
    }

    drawer_footer = ()=>{
        return (
            <div style={{textAlign: 'right'}}>
                <Button onClick={()=>this.drawerRef ? this.drawerRef.submit() : this.setState({visible:false})} type="primary">
                    Submit
                </Button>
            </div>
        )
    }

    onClose=()=>{
        this.setState({
            visible:false,
            drawer_content: null,
            drawer_foot: null,
        })
    }

    render() {
        return (
            <React.Fragment>
                <Table
                    dataSource={this.state.user_list}
                    pagination={
                        {
                            current: this.state.current_page,
                            pageSize: this.state.page_size,
                            total: this.state.rs_count,
                            onChange: this.handlePageChange,
                        }
                    }
                    columns={this.userColumns}
                    rowKey={record => record.id}
                />
                <Drawer
                    destroyOnClose={true}
                    visible={this.state.visible}
                    width="400px"
                    title={this.state.drawer_title}
                    placement="right"
                    closable={true}
                    onClose={this.onClose}
                    footer={this.state.drawer_foot}>
                    {this.state.drawer_content}
                </Drawer>
            </React.Fragment>
        )
    }
}

