export class NewUser extends Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.formRef = React.createRef()
        this.state = {
            img_url: this.globalState.domain + '/api/captcha'
        }
    }

    handleUpdateImg = () => {
        this.setState({
            img_url: this.globalState.domain + '/api/captcha?ts=' + new Date().valueOf()
        })
    }

    submit = () => {
        this.formRef.submit()
        this.handleUpdateImg();
    }

    onFinish = (values) => {
        let url = this.globalState.domain + '/api/user/register'
        if (this.props.type === 'subuser') {
            url = url + '?sub_user=1'
        }
        axios.post(
            url,
            values,
            this.props.type === 'subuser' ? this.globalState.request_headers : {}
        ).then(ret => {
            message.success('success')
            this.props.onSuccess();
            // this.formRef.resetFields()
        }).catch(e => {
            message.warning(e.response.data.detail)
        })
    }

    render() {
        return (
            <div>
                <Form
                    ref={ref => this.formRef = ref}
                    layout="vertical"
                    onFinish={this.onFinish}
                >
                    <Form.Item
                        name="first_name"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your name!',
                            },
                        ]}
                        label="Name"
                    >
                        <Row gutter={[8,8]}>
                        <Col span={24}>
                            <Input placeholder="name"/>
                        </Col>
                        <Col span={24}>
                            <Alert description={'first name, max length 8 characters'} type={'info'} />
                        </Col>
                        </Row>
                    </Form.Item>
                    <Form.Item
                        name="username"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your username!',
                            },
                        ]}
                        label="UserName"
                    >
                        <Row gutter={[8,8]}>
                            <Col span={24}>
                                <Input placeholder="username"/>
                            </Col>
                            <Col span={24}>
                                <Alert description={'login username, 6-20 characters'} type={'info'} />
                            </Col>
                        </Row>
                    </Form.Item>
                    <Form.Item
                        name="email"
                        label="E-mail"
                        rules={[
                            {
                                type: 'email',
                                message: 'The input is not valid E-mail!',
                            },
                            {
                                required: true,
                                message: 'Please input your E-mail!',
                            },
                        ]}
                    >
                        <Input placeholder="email"/>
                    </Form.Item>

                    <Form.Item
                        name="pwd"
                        label="Password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                        hasFeedback
                    >
                        <Input.Password placeholder="password"/>
                    </Form.Item>

                    <Form.Item
                        name="confirm"
                        label="Confirm Password"
                        dependencies={['pwd']}
                        hasFeedback
                        rules={[
                            {
                                required: true,
                                message: 'Please confirm your password!',
                            },
                            ({getFieldValue}) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('pwd') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                },
                            }),
                        ]}
                    >
                        <Input.Password placeholder="confirm password"/>
                    </Form.Item>

                    <Form.Item
                        name="phone"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your telephone!',
                            },
                        ]}
                        label="Telephone"
                    >
                        {/*<Input addonBefore={prefixSelector} placeholder="telephone"/>*/}
                        <Input placeholder="telephone"/>
                    </Form.Item>

                    <Form.Item
                        name="verify_code"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your captcha code!',
                            },
                        ]}
                        label="Verification code"
                    >
                        <Row gutter={16}>
                            <Col span={16}>
                                <Input placeholder={'Verification code'}/>
                            </Col>
                            <Col span={8}>
                                <img alt={'verify img'} src={this.state.img_url} onClick={this.handleUpdateImg}
                                       style={{width: 100, height: 32}}/>
                            </Col>
                        </Row>
                    </Form.Item>

                </Form>
            </div>
        );
    }
}


export class SubUsers extends Component {

    constructor(props) {
        super(props);
        this.globalState = store.getState();
        this.state = {
            visible: false,
        }
        this.newUserRef = React.createRef()
        this.usersRef = React.createRef()
    }

    onSubmit = () => {
        this.newUserRef.submit()
    }

    handleOpenDrawer = () => {
        this.setState({
            visible: true
        })
    }

    handleCloseDrawer = () => {
        this.setState({
            visible: false,
        })

    }

    subComponentSuccess=()=>{
        this.handleCloseDrawer();
        this.usersRef.getUserList();
    }



    render() {
        return (
            <Card title={"Sub Users"}>
                <Button type={"primary"} style={{marginBottom: '20px'}}
                        onClick={this.handleOpenDrawer}><UserAddOutlined/>Add sub user</Button>
                <Users ref={ref => this.usersRef = ref} type={'subuser'}/>
                <Drawer
                    destroyOnClose={true}
                    width="400px"
                    title={`New sub user`}
                    placement="right"
                    closable={true}
                    onClose={this.handleCloseDrawer}
                    visible={this.state.visible}
                    footer={(<div style={{textAlign: 'right'}}>
                        <Button onClick={this.onSubmit} type="primary">
                            Submit
                        </Button>
                    </div>)}
                >
                    <NewUser onSuccess={this.subComponentSuccess} ref={ref => this.newUserRef = ref} type={'subuser'}/>
                </Drawer>
            </Card>
        )
    }
}