export function toPercent(point) {
    if (point === 0) {
        return '0';
    }
    let str = parseInt(point * 100).toFixed();
    str += "%";
    return str;
}


export function formatBytes(bytes,label=true) {
    if (bytes === '0' || isNaN(bytes) || bytes === undefined || bytes === 0) return '0';
    let k = 1024;
    let sizes = ['B','KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    let i = Math.floor(Math.log(bytes) / Math.log(k))
    let x = (bytes / Math.pow(k, i)).toFixed(2)
    if (label){
        return x + ' ' + sizes[i];
    }else{
        return x
    }
}


