import {Card, Alert, message} from "antd";
import React from "react";
import axios from "axios";
import store from "../../store";
import {formatBytes} from "../funs";

const styles={
    field:{
        display: 'inline-block',
        width: 100,
        textAlign: 'right',
        marginRight: 30,
    }
}

export class BucketDetail extends React.Component{
    constructor(props) {
        super(props);
        this.globalState=store.getState();
        this.state = {
            data: {},
            usage:{}
        }
    }

    componentDidMount() {
        this.getBucketDetailInfo();
    }

    getBucketDetailInfo=()=>{
        axios.get(
            this.globalState.domain+'/api/buckets/detail?bucket_name='+this.props.bucket,
            this.globalState.request_headers
        ).then(res=>{
            if (res.data.data !== null && res.data.data !== undefined ){
                this.setState({
                    data: res.data.data,
                    usage: res.data.data['usage']['rgw.main']
                })
            }
        }).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    render() {
        return(
            <Card style={{border: 0}}>
                <p><b style={styles.field}>Name</b> {this.state.data.bucket}</p>
                <p><b style={styles.field}>Index type</b>{this.state.data.index_type}</p>
                <p><b style={styles.field}>shards total</b>{this.state.data.num_shards}</p>
                {
                    this.state.usage !== undefined ? (
                        <div>
                            <p><b style={styles.field}>Objects total</b>{this.state.usage.num_objects}</p>
                            <p><b style={styles.field}>Actual used</b>{formatBytes(this.state.usage.size_actual)}</p>
                            {/*<p>Size_utilized: {formatBytes(this.state.usage.size_utilized)}</p>*/}
                            {/*<p>Size: {formatBytes(this.state.usage.size)}</p>*/}
                        </div>
                    ) : null
                }

                <Alert
                    showIcon
                    type="info"
                    message={'Information'}
                    description={"The above information comes from the back-end storage server"}
                />
            </Card>
        )
    }
}